import { Component, OnInit, Renderer2 } from '@angular/core';
import { LocalStorageService } from 'src/app/core/services/local-storage.service';
import { BaseGrid } from 'src/app/shared/components/basegrid';
import { Router } from '@angular/router';
import { GridSettings } from 'src/app/core/models/grid-settings.interface';
import { DateFormatService } from 'src/app/core/services/dateFormat.service';
import { ToastMessageService } from '../../../../../../core/services/toast-message.service';
import { AppUtility } from 'src/app/core/services/utility.services';
import { UserFieldService } from 'src/app/shared/http/userfield.service';
import * as moment from 'moment';
import { BankAccountTypeMasterModel } from '../../../../models/bank-account-type-master.model'
import { BankAccountTypeMasterService } from '../../../../http/bank-account-type-master.service'

@Component({
  selector: 'app-bank-account-type-master-list',
  templateUrl: './bank-account-type-master-list.component.html',
  styleUrls: ['./bank-account-type-master-list.component.scss']
})
export class BankAccountTypeMasterListComponent extends BaseGrid implements OnInit {

  public gridDataList = [];
  public orginalGridList = [];
  public mbloadingEnabled = false;
  public gridSettings: GridSettings = new GridSettings();
  public mscustomDateFormat: string = '';
  public mbShowDeleteDialog: boolean = false;
  public mObjModel: BankAccountTypeMasterModel = new BankAccountTypeMasterModel();
  public maarraySelected: Array<any> = [];
  public mbBulkDelete: boolean = false;
  public msScreenName: string = 'Bank Account Type Master';
  public msI18Name: string = 'BankAccountTypeMaster';
  public msLookUpKey: string = 'BankAccountTypeList';
  public msRoutePath: string = '/main/bankcash/bank-account-type-master';
  public msManageRoutePath: string = '/main/bankcash/bank-account-type-master/manage';
  public msMsgData: any = {};
  public mbBulkDeleteView: boolean = false;
  public bmlfSelectedCallback = (args) => args.dataItem;

  constructor(private router: Router,
    public renderer: Renderer2,
    private lsService: LocalStorageService,
    private mObjService: BankAccountTypeMasterService,
    public dateFormatService: DateFormatService, public objToastService: ToastMessageService,
    public objUtil: AppUtility,
    public userFieldService: UserFieldService) {
    super(lsService, dateFormatService, userFieldService, objToastService, objUtil);
  }

  ngOnInit() {
    this.mscustomDateFormat = this.dateFormatService.getDateFormat();
    this.initGrid();
    this.objUtil.getBMEScreePermission(this.msScreenName);
    this.getDataList();
    this.setExportData({ 'pageName': this.msScreenName });
  }

  getDataList(): void {

    this.mbloadingEnabled = true;
    const headerData = { "ScreenName": this.msScreenName, "LookUpKey": this.msLookUpKey, "Filter1": "", "Filter2": "", "Filter3": "", "Filter4": "", "Filter5": "" };

    this.mObjService.bmlfonGetList(headerData).subscribe(

      res => {
        let parrGridData = res.LookupData;
        parrGridData.forEach(ele => {
          ele.RecDate = moment(new Date(ele.RecDate)).format(this.mscustomDateFormat.toUpperCase());
        });

        this.gridDataList = parrGridData;
        this.orginalGridList = parrGridData;
        let parrSettingData = res.GridSettingData[0].JSONString;
        this.setInitGridSettings(parrSettingData, this.msI18Name);
        this.mobjGridSettings.state.take=50;
      }
      , error => {
        console.log(error);
      }

    ).add(() => { this.mbloadingEnabled = false; });
  }

  bmlfResetGridSettings() {
    this.bmlfOnResetGridSettings(this.msScreenName, this.msI18Name);
  }

  bmlfSaveGridSetting(): void {
    this.bmlfOnSaveGrid(this.msScreenName);
  }

  //Add
  bmlfonAdd(): void {
    //check Write permission
    if (!this.objUtil.mbWrite) {
      this.objToastService.notifyWarning(this.objUtil.bmgfonTranslate('COMMON.PERMISSION_WRITE'));
      return;
    };
    this.router.navigate([this.msManageRoutePath]);
  }

  //Edit
  bmlfonEdit(data) {
    this.router.navigate([this.msManageRoutePath], { queryParams: { BankAcTypeID: data.BankAcTypeID } });
  }

  //copy
  bmlfonCopy(data) {
    //check Write permission
    if (!this.objUtil.mbWrite) {
      this.objToastService.notifyWarning(this.objUtil.bmgfonTranslate('COMMON.PERMISSION_WRITE'));
      return;
    };
    this.router.navigate([this.msManageRoutePath], { queryParams: { BankAcTypeID: data.BankAcTypeID, CopyRecord: "Y" }, skipLocationChange: true });
  }

  //Delete 
  bmlfonDelete(data) {
    if (!this.bmlfCheckModification(data.BankAcTypeID)) {
      this.objToastService.notifyWarning(this.objUtil.bmgfonTranslate('BankAccountTypeMaster.Msg.DeleteCheck'));
      return;
    }
    //check delete permission
    if (!this.objUtil.mbDelete) {
      this.objToastService.notifyWarning(this.objUtil.bmgfonTranslate('COMMON.PERMISSION_DELETE'));
      return;
    };
    this.mObjModel.BankAcTypeID = data.BankAcTypeID;
    this.mbShowDeleteDialog = true;
  }

  //Bulk Delete
  onActionDelete() {
    //check delete permission
    if (!this.bmlfCheckModification(0)) {
      this.objToastService.notifyWarning(this.objUtil.bmgfonTranslate('BankAccountTypeMaster.Msg.DeleteCheckRang'));
      return;
    }

    if (!this.objUtil.mbDelete) {
      this.objToastService.notifyWarning(this.objUtil.bmgfonTranslate('COMMON.PERMISSION_DELETE'));
      return;
    };

    if (this.maarraySelected.length == 0) {
      this.objToastService.notifyError(this.objUtil.bmgfonTranslate('COMMON.MSG.NODATAFORDELETE'));
      return;
    }
    this.mbBulkDelete = true;
    this.mbShowDeleteDialog = true;
  }

  bmlfonfilterEvent(obj) {
    if (obj.Type == "String") {
      let filterGrid = this.orginalGridList.filter(x => x[obj.FirstKey] == obj.FirstValue);
      this.gridDataList = filterGrid;
    }
    else if (obj.Type == "Date") {
      let filterGrid = this.orginalGridList.filter(x => (new Date(x[obj.FirstKey]).getTime() >= obj.FirstValue.getTime()) && new Date(x[obj.FirstKey]).getTime() <= obj.SecondValue.getTime());
      this.gridDataList = filterGrid;
    }
  }

  bmlfonDialogAction(action) {
    
    if (action == 'No') {
      this.mbShowDeleteDialog = false;
    } else if (action == 'Yes') {
      if (this.mbBulkDelete == true) {
        let reqData: any = {
          JSON: JSON.stringify({
            HeaderData: this.maarraySelected,
            Response: [{
              "ResponseText": "", "ErrorCode": ""
            }]
          })
        };
        this.mObjService.bmlfonDeleteData(reqData).subscribe(res => {
          if (res == null) {
            this.objToastService.notifyError('Server Error');
            return false;
          }
          if (res.Response[0].ErrorCode == "") {
            //this.objToastService.notifySuccess(res.Response[0].ResponseText);
            this.msMsgData.Data = res.Response[0].ResponseText;
            this.msMsgData.Header = "BatchMaster";
            this.msMsgData.mbBulkDeleteView = true;
            this.mbBulkDeleteView = true;

            this.mbShowDeleteDialog = false;
            this.mbBulkDelete = false;
            this.maarraySelected = [];
            this.getDataList();
          } else {
            this.objToastService.notifyError(res.Response[0].ResponseText);
            this.mbShowDeleteDialog = false;
            this.getDataList();
          }
        });
      }
      else {
        let reqData: any = {
          JSON: JSON.stringify({
            HeaderData: [{ ... this.mObjModel }],
            Response: [{
              "ResponseText": "", "ErrorCode": ""
            }]
          })
        };
        this.mObjService.bmlfonDeleteData(reqData).subscribe(res => {
          if (res == null) {
            this.objToastService.notifyError('Server Error');
            return false;
          }
          if (res.Response[0].ErrorCode == "") {
            {
              if (res.Response[0].ResponseText.toUpperCase().search('DELETED') != -1) {
                this.objToastService.notifySuccess((this.objUtil.bmgfonTranslate('COMMON.MSG.DELETE')));
                this.mbShowDeleteDialog = false;
                this.mbBulkDelete = false;
                this.maarraySelected = [];
                this.getDataList();
              } else {
                this.objToastService.notifyError(res.Response[0].ResponseText);
                this.mbShowDeleteDialog = false;
              }
            }
          } else {
            this.objToastService.notifyError(res.Response[0].ResponseText);
          }
        });
      }

    }
  }

  bmlfonselectionChange(e) {
    if (e.selectedRows.length > 0) {
      e.selectedRows.forEach(ele => {
        this.maarraySelected.push(ele.dataItem);
      });
    }
    else if (e.deselectedRows.length > 0) {
      e.deselectedRows.forEach(ele => {
        let pobjTempData = ele.dataItem;
        this.maarraySelected = this.maarraySelected.filter(x => {
          if (x.BankAcTypeID != pobjTempData.BankAcTypeID) {
            return x;
          }
        });
      });
    }
  }

  public bmlfoncloseModal(event) {
    this.mbBulkDeleteView = event.mbBulkDeleteView;
  }

  pageChange(e) {
    this.maarraySelected = [];
  }

  bmlfCheckModification(BankAcTypeID: Number): boolean {
    
    let pbFlag: boolean = false;
    if (BankAcTypeID == 0) {
      let parrFilterData: any = [];
      parrFilterData = this.maarraySelected.filter(obj => obj["BankAcTypeID"] > 0 && obj["BankAcTypeID"] < 100);
      if (parrFilterData.length == 0) {
        pbFlag = true;
      }
    } else {
      if (BankAcTypeID >= 100) {
        pbFlag = true;
      } else {
        pbFlag = false;
      }
    }
    return pbFlag;
  }

  bmlFilterSetting() {
    this.filterEnabled = !this.filterEnabled;
  }
}
