  bmlfonCloseLookupModal(pobjData) {
    this.mbIsLookupModal = pobjData.isLookupModal;
  }
  // Get Lookup Description
  bmlfGetLookupDes(pobjData) {
    let pobjReqData = {
      ScreenName: 'VoidChequeTranComponent',
      LookUpKey: 'BankName',
      Filter1: this.mObjModel.BankID
    }
    this.userService.GETGeneralSqlData(pobjReqData).subscribe(res => {
      if (res.LookupData.length > 0) {
        this.msBankName = res.LookupData[0].BankName;
        this.msBankDesc = res.LookupData[0].Description;
      }
    });
  }

  bmlfonProcess() {
    //check Write permission
    if (!this.appUtility.mbWrite) {
      this.toastMessageService.notifyWarning(this.appUtility.bmgfonTranslate('COMMON.PERMISSION_WRITE'));
      return;
    };
    if (this.mbChange || this.FieldExtender.mbChange || this.customCtrl.mbChange) {
      this.msCallFor = "PROCESS";
      this.mbShowDialog = true;
      return;
    };

    let pRecID: any = this.mObjModel.RecID;

    if (pRecID > 0) {
      let reqData: any = {
        JSON: JSON.stringify({
          HeaderData: [{
            RecID: pRecID
          }],
          Response: [{
            "ResponseText": "", "ErrorCode": ""
          }]
        })
      };
      this.objService.bmlfonProcessData(reqData).subscribe(res => {
        if (res == null) {
          this.toastMessageService.notifyError('Server Error');
          return false;
        }
        if (res.Response[0].ErrorCode == "") {
          this.mbDisabledPrimaryKey = true;
          this.msMsgData.screenName = this.msScreenName;
          this.msMsgData.Data = res.Response[0].ResponseText;
          this.msReportDialog = res.Response[0].ResponseText;
          if ((this.msReportDialog.length || this.msMsgData.Data.length) > this.mnumResponseLen) {
            this.bmlfReportModal('open');
            this.mbBulkResponseView = true;
          } else {
            this.toastMessageService.notifySuccess(res.Response[0].ResponseText);
          }
          this.bmlfonClearControl();
        }
        else {
          let psErrorMsg = res.Response[0].ResponseText.toString().replace('Failure :', '');
          this.toastMessageService.notifyError(psErrorMsg);
          this.mbShowDeleteDialog = false;
        };
      });
    }

  }

  bmlfSetUserData() {
    if (this.msFormMode == "I") {
      this.mObjModel.Recuserid = this.mobjUserData.UserId || ''
      this.mObjModel.Recdate = new Date();
      this.mObjModel.CreateUserID = this.mobjUserData.UserId || ''
      this.mObjModel.CreateDate = new Date();
    }
    else {
      this.mObjModel.ModifyUserID = this.mobjUserData.UserId || ''
      this.mObjModel.ModifyDate = new Date();
    }
  }

  // Report expanded Modal
  bmlfReportModal(psKey) {

    if (psKey == 'open') {
      this.mbReportDialog = true;
    } else if (psKey == 'close' || psKey == 'ok') {
      this.mbReportDialog = false;
    }
  }

  public bmlfGetCheqDetails(): void {

    this.mbShowWarning = false;
    this.msDocType = "";
    this.mObjModel.IssueDocNo = "";
    this.msChqStatus = "";
    if (this.mObjModel.BankID > 0 && this.mObjModel.ChqBookID != "" && this.mObjModel.ChqNumber != "") {
      let pobjReqData = {
        ScreenName: 'VoidChequeTranComponent',
        LookUpKey: 'ChequeDetails',
        Filter1: this.mObjModel.BankID,
        Filter2: this.mObjModel.ChqBookID,
        Filter3: this.mObjModel.ChqNumber
      }
      this.userService.GETGeneralSqlData(pobjReqData).subscribe(res => {
        if (res == null) {
          return;
        }
        if (res.LookupData.length > 0) {
          this.bmlfSetDocType(res.LookupData[0].DocType);
          this.mObjModel.IssueDocNo = res.LookupData[0].IssueDocNo;
          this.msChqStatus = res.LookupData[0].ChqStatus;
          this.bmlfSetChqStatus();
        }
      });
    }
  }

  bmlfSetDocType(DocType: string) {
    if (DocType.toUpperCase() == "PAYMENT") {
      this.msDocType = "Payment Entry";
    } else if (DocType.toUpperCase() == "CONTRA") {
      this.msDocType = "Contra Entry";
    } else if (DocType.toUpperCase() == "CHECKPRINT") {
      this.msDocType = "Payment Range Processing";
    }
  }

  bmlfSetChqStatus() {
    if (this.mObjModel.IssueDocNo.trim() == "") {
      this.msChqStatus = "";
      return;
    }
    if (this.msChqStatus == "0") {
      this.msChqStatus = "UNPOSTED";
    } else if (this.msChqStatus == "1") {
      this.msChqStatus = "POSTED";
      if (this.msDocType == "Payment Entry" || this.msDocType == "Contra Entry") {
        this.mbShowWarning = true;
      }
    }
  }

  //Open Task MODAL 
  bmlfOpenTaskModal(taskType) {
    this.mobjTaskData = {};
    this.mobjTaskData.taskType = taskType;
    if (taskType == 'myTask') {
      this.mobjTaskData.title = this.appUtility.bmgfonTranslate('MYTASK');
      this.mobjTaskData.innerRecord = this.mobjUserData;
    } else if (taskType == 'recordTask') {
      this.mobjTaskData.title = this.appUtility.bmgfonTranslate('TASKFORRECORD');
      let pobjFormRec: any = {};
      pobjFormRec.formName = this.msScreenName;
      pobjFormRec.primeKey = this.mObjModel.ChqBookID + "" + this.mObjModel.ChqNumber;
      this.mobjTaskData.innerRecord = pobjFormRec;
    }
    this.mbIsTaskModal = true;
  }

  //Close Task MODAL 
  bmlfonCloseTaskModal(pobjData) {
    this.mbIsTaskModal = pobjData.isTaskModal;
  }
  // check e signature
  bmlfCheckEsignApplicableOnForm() {
    let pobjReqData: any = {
      JSON: JSON.stringify({
        HeaderData: [{
          MenuScreenName: this.msScreenName,
        }],
        Response: [{
          ResponseText: '',
          ErrorCode: ''
        }]
      })
    };
    this.userService.bmlfCheckEsignApplicable(pobjReqData).subscribe(res => {
      if (res == null) {
        return false;
      }
      if (res.Response[0].ErrorCode == '') {
        this.mbIsEsignature = res.ESignData[0].Permision;
        this.mbIsEsignADTPermision = res.ESignData[0].ADTPermision;
        this.mobjSignatureData.reasonMinLength = res.ESignData[0].ReasonLength;
        this.mobjSignatureData.reqSignAuth = res.ESignData[0].NoOfReqSign;
      }
    }, err => {
      console.log(err);
    });
  }

  // open e signature modal
  bmlfopenESignatureModal() {
    this.mbSignatureModal = true;
    this.mobjSignatureData.isShow = true;
    this.mobjSignatureData.primaryKey = this.mObjModel.RecID;
  }

  // check e signature
  bmlfChkESignature(pobjData) {
    this.mbSignatureModal = pobjData.isShow;
    if (pobjData.event == 'Save') {
      this.mbIsEsignature = false;
      this.mObjModel.ESG_REASON = pobjData.changeReason;
      this.mObjModel.ESG_APPROVER = pobjData.eSignApprover;
      this.mbChange = true;
      this.bmlfonSubmitData();
    } else if (pobjData.event == 'Cancel') {
      this.mobjCommonAlertData = {}
      this.mbCommonAlertDialog = true;
      this.mobjCommonAlertData.message = this.appUtility.bmgfonTranslate('ESIGNREASONMSG');
      this.mobjCommonAlertData.event = 'close';
      this.mbChange = true;
    }
  }

