  bmlfReportModal(psKey) {
    if (psKey == 'open') {
      this.mbBulkResponseView = true;
    } else if (psKey == 'close' || psKey == 'ok') {
      this.mbBulkResponseView = false;
      if (this.mbCallFromPost) {
        if (this.msMsgData.Data.toUpperCase().search('SUCCESSFULLY') != -1) {
          let jsonObj = { JrnlBtchNo: this.mObjModel.JrnlBtchNo, Status: 'POSTED' };
          this.appUtility.refreshUrlAndInitCustom('/main/bankcash/refresh', this.msRoutePathManage, true, jsonObj);
          this.msStatus = 'POSTED';
          this.bmlfGetDataOnLoad(this.mObjModel.JrnlBtchNo);
        }
      }
      this.mbCallFromPost = false;
    }
  }

