  public bmlfGetBankCashBalance(AccountType): void {

    let pnumBankCurrentBal: number = 0;
    let pnumHomeCurrentBal: number = 0;
    let AccountNumber: string = "";
    if (AccountType == "B") {
      AccountNumber = this.mObjModel.Acct;
    } else if (AccountType == "T") {
      AccountNumber = this.mObjModel2.Acct;
    }

    let dtFromDate = moment(this.mObjModel.AplDate).format(CommonConstants.gsServerDateFormat);

    if (AccountNumber == '') {
      return;
    }

    let reqData: any = {
      JSON: JSON.stringify({
        HeaderData: [{ GLAccount: AccountNumber, AplDate: dtFromDate }], Response: [{ "ResponseText": "", "ErrorCode": "" }]
      })
    };
    this.objService.bmlfonGetBankCashBalance(reqData).subscribe(res => {
      if (res == null) {
        this.toastMessageService.notifyError('Server Error');
        return false;
      } else if (res.Response[0].ErrorCode == "") {
        if (res.HeaderData.length > 0) {
          this.bmlfGetHomeCurrancy();
          let psHomeCRDR: string = " Dr.";
          let psBankCRDR: string = " Dr.";

          pnumBankCurrentBal = res.HeaderData[0].BalanceBankCurrency;
          pnumHomeCurrentBal = res.HeaderData[0].BalanceHomeCurrency;
          if (AccountType == "B") {
            this.mnumBankCurrentBal = Number(pnumBankCurrentBal);
          }
          if (pnumBankCurrentBal < 0) {
            pnumBankCurrentBal = pnumBankCurrentBal * -1;
            psBankCRDR = " Cr."
          }
          if (pnumHomeCurrentBal < 0) {
            pnumHomeCurrentBal = pnumHomeCurrentBal * -1;
            psHomeCRDR = " Cr."
          }
          if (AccountType == "B") {
            if(pnumBankCurrentBal == null){
              this.msCurrentBalance1 = '';
            }else{
              this.msCurrentBalance1 = this.mObjModel.AcctCurrency + " " + pnumBankCurrentBal.toFixed(2).toString() + psBankCRDR + " | " + this.msHomeCurrency + " " + pnumHomeCurrentBal.toFixed(2).toString() + psHomeCRDR;
            }
          } else if (AccountType == "T") {
            this.msCurrentBalance2 = this.mObjModel2.AcctCurrency + " " + pnumBankCurrentBal.toFixed(2).toString() + psBankCRDR + " | " + this.msHomeCurrency + " " + pnumHomeCurrentBal.toFixed(2).toString() + psHomeCRDR;
          }
        }
      }
    });
  }

  public bmlfGetHomeCurrancy(): void {
    //+++++++++++++ Home Currency  ++++++++++++++++++++++++++
    let pobjReqData = {
      ScreenName: 'ContraEntryComponent',
      LookUpKey: 'CompanyInfo'
    }
    this.userService.GETGeneralSqlData(pobjReqData).subscribe(res => {
      if (res.LookupData.length > 0) {
        this.msHomeCurrency = res.LookupData[0].HomeCurrency;
      }
    });
  }

  public bmlfGetPaymentModeID(): void {
    //+++++++++++++ Home Currency  ++++++++++++++++++++++++++
    let pobjReqData = {
      ScreenName: 'ContraEntryComponent',
      LookUpKey: 'PaymentModeID',
      Filter1: this.mObjModel.Vend_CustKey
    }
    this.userService.GETGeneralSqlData(pobjReqData).subscribe(res => {
      if (res.LookupData.length > 0) {
        this.mnumPaymentModeID = res.LookupData[0].PaymentModeID;
      }
    });
  }


  public bmlfGetConversionFactor(): void {
    //+++++++++++++ Conversion Factor  ++++++++++++++++++++++++++

    if (this.mObjModel.AcctCurrency == "" || this.mObjModel2.AcctCurrency == "") {
      return;
    }

    let reqData: any = {
      JSON: JSON.stringify({
        HeaderData: [{
          FromCurrency: this.mObjModel.AcctCurrency,
          ToCurrency: this.mObjModel2.AcctCurrency,
          DATE: new Date(),
          RateType: "B",
          ConversionFactor: "0.00",
        }],
        Response: [{ "ResponseText": "", "ErrorCode": "" }]
      })
    };
    this.objService.bmlfonGetFinanceCurrencyConversion(reqData).subscribe(res => {
      if (res == null) {
        this.toastMessageService.notifyError('Server Error');
        return false;
      } else if (res.Response[0].ErrorCode == "") {
        if (res.HeaderData.length > 0) {
          this.mObjModel.AcctCurrencyFact = Number(res.HeaderData[0].ConversionFactor);
        }
      }
    });
  }

  public bmlfGetHomeConversionAmt(): void {
    //+++++++++++++ Conversion Factor  ++++++++++++++++++++++++++

    this.bmlfGetHomeCurrancy();
    if (this.mObjModel.AcctCurrency == "" || this.msHomeCurrency == "") {
      return;
    }

    let reqData: any = {
      JSON: JSON.stringify({
        HeaderData: [{
          FromCurrency: this.mObjModel.AcctCurrency,
          ToCurrency: this.msHomeCurrency,
          DATE: new Date(),
          RateType: "B",
          ConversionFactor: "0.00",
        }],
        Response: [{ "ResponseText": "", "ErrorCode": "" }]
      })
    };
    this.objService.bmlfonGetFinanceCurrencyConversion(reqData).subscribe(res => {
      if (res == null) {
        this.toastMessageService.notifyError('Server Error');
        return false;
      } else if (res.Response[0].ErrorCode == "") {
        if (res.HeaderData.length > 0) {
          let pnumCurrFact: number = 0;
          pnumCurrFact = Number(res.HeaderData[0].ConversionFactor);
          if (this.mObjModel.AcctCurrencyAmt < 0) {
            this.mObjModel2.TrnAmt = pnumCurrFact * this.mObjModel.AcctCurrencyAmt * -1
            this.mObjModel.TrnAmt = pnumCurrFact * this.mObjModel.AcctCurrencyAmt
          } else {
            this.mObjModel2.TrnAmt = pnumCurrFact * this.mObjModel.AcctCurrencyAmt
            this.mObjModel.TrnAmt = pnumCurrFact * this.mObjModel.AcctCurrencyAmt * -1
          }
        }
      }
    });
  }

