    this.msCustomDateFormat = this.dateFormatService.getDateFormat();
    this.mobjUserData = this.lsService.getUserSession();
    this.msSaved = this.activeRoute.snapshot.queryParamMap.get("Saved") == null ? '' : this.activeRoute.snapshot.queryParamMap.get("Saved");

    if (this.msSaved == 'false') {
      this.mbIsHome = true;
      this.mObjModel.Currency = 'HomeCurrency';
      this.msFormMode = "I";
      this.mbInsert = true;
      this.mbChange = false;
      this.mbDisabledPrimaryKey = false;
      this.mbCustomControl = true;
      this.bmlfonURL("");
      this.msDropdownCaption = this.appUtility.bmgfonTranslate('TRANRECONCILIATION.BANKACCOUNT');
      this.mObjModel.ReconcileType = "Bank Accounts";
      this.msCaptionForReconcileTypeNo = "Bank Accounts";
      this.msReconcileType = "GL";
      this.msLookUpKey = "BankAccount";
      setTimeout(function () { document.getElementById('txtReconcileDocNo').focus(); }, 10);
    }
    else if (this.msSaved == 'true') {
      this.msReconcileDocNo = this.activeRoute.snapshot.queryParamMap.get("ReconciliationNo") == null ? '' : this.activeRoute.snapshot.queryParamMap.get("ReconciliationNo");
      this.bmlfGetReconciliationDetails();
    }
