  public bmlfGetReconciliationDetails(): void {
    //+++++++++++++ Home Currency  ++++++++++++++++++++++++++    
    let pobjReqData = {
        ScreenName: 'TransactionReconcileComponent',
        LookUpKey: 'ReconciliationData',
        Filter1: this.msReconcileDocNo
    }
    this.userService.GETGeneralSqlData(pobjReqData).subscribe(res => {
      if (res.LookupData.length > 0) {
        this.mObjModel.ReconcileDocNo = this.msReconcileDocNo;
        this.msReconcileTypeNo = res.LookupData[0].ReconcileTypeNo;
        this.mObjModel.ReconcileTypeNo = this.msReconcileTypeNo;
        if (res.LookupData[0].ReconcileType == "Vendor") {
          this.msReconcileType = "AP";
        } else if (res.LookupData[0].ReconcileType == "Customer") {
          this.msReconcileType = "AR";
        } else {
          this.msReconcileType = "GL";
        }
        // this.msReconcileType = res.LookupData[0].ReconcileType;
        this.mObjModel.ReconcileType = res.LookupData[0].ReconcileType;
        this.msFromdate = res.LookupData[0].Fromdate;
        this.mObjModel.Fromdate = new Date(this.msFromdate);
        this.msTodate = res.LookupData[0].Todate;
        this.mObjModel.Todate = new Date(this.msTodate);
        this.mObjModel.Currency = res.LookupData[0].Currency;
        this.bmlfonOnClickEvent_RadioButton();
        this.modalService.open('TransactionDetail');
        this.bmlfValidateChargesandIncome();
        this.mbChangeTranDetails = false;
        this.bmlfGetDataAfterSave();
      }
    });
  }

  bmlfOpenLookupModal(psField, psScreenName, psLookUpKey, psEvent, psValue, psType, psEleId, pnumRowIdx = null) {
    
    this.mbIsLoading = true;
    this.marrLookupGridData = [];
    this.mobjLookupKeyData = {};
    if (psEvent == 'blur' && (psValue == undefined || psValue == '')) {
      this.mobjLookupDataModel[psField] = "";
      return false;
    }
    psLookUpKey = this.msLookUpKey;

    let psReqData: any;

    if (psLookUpKey == "BankAccount" || psLookUpKey == "CustomerKey" || psLookUpKey == "GLAccount" || psLookUpKey == "VendorKey") {
      psReqData = { "ScreenName": this.msScreenName, "LookUpKey": psLookUpKey, "Filter1": "", "Filter2": "", "Filter3": "", "Filter4": "", "Filter5": "" };
    }

    this.objLookupService.getLookup(psReqData).subscribe(res => {
      this.mbIsLoading = false;
      if (res == null) {
        this.bmlfOpenErrorBlock('COMMON.MSG.NODATA');
        return false;
      }
      if (Object.entries(res).length > 0 && res.LookupData.length > 0) {
        // let psLookupHeading = psField;
        res.LookupData.forEach(ele => {
          //ele.Recdate = moment(ele.Recdate).format(this.msCustomDateFormat.toUpperCase());
          if (psLookUpKey == "BankAccount") {
            if (!CustomValidator.bmgfNullCheckValidator(ele.Last_Reconciled_Date)) {
              ele.Last_Reconciled_Date = moment(ele.Last_Reconciled_Date).format(this.msCustomDateFormat.toUpperCase());
            } else {
              ele.Last_Reconciled_Date =  moment(new Date()).format(this.msCustomDateFormat.toUpperCase());
            }
           
          }
        });
        // if (psField == this.msLookupCaption) {
        //   psLookupHeading = this.msLookupCaption;
        // } else if (psField == "CustomerKey") {
        //   psLookupHeading = this.msLookupCaptionForItemKey;
        // } else if (psField == "Itemkey") {
        //   psLookupHeading = this.msLookupCaptionForItemkey;
        // }
        this.marrLookupGridData = res.LookupData;
        this.mnumLineItemRowIndex = pnumRowIdx;
        this.mobjLookupKeyData.field = psField;
        this.mobjLookupKeyData.gridData = this.marrLookupGridData;
        this.mobjLookupKeyData.type = psType;
        this.mobjLookupKeyData.eleId = psEleId;
        if (psValue) {
          this.mobjLookupKeyData.searchValue = psValue;
        } else {
          this.mobjLookupKeyData.searchValue = '';
        }

        if (psEvent == 'blur' && psValue) {
          let parrFilterdArr: any = [];
          /** --- Check : enter key matching record found. if find single row then direct call save lookup function --*/
          let pobjFirstInput = Object.keys(this.marrLookupGridData[0])[0];
          parrFilterdArr = this.marrLookupGridData.filter(obj => {
            if (obj[pobjFirstInput].toUpperCase() == psValue.toUpperCase()) {
              return obj;
            }
          });
          if (parrFilterdArr.length == 1) {
            this.mobjLookupKeyData.value = parrFilterdArr[0];
            this.mobjLookupKeyData.valueKey = parrFilterdArr[0][pobjFirstInput];
            this.bmlfSaveLookupField(this.mobjLookupKeyData);
            this.mbIsLookupModal = false;
          } else {
            this.mbIsLookupModal = true;
          }
        } else if (psEvent == 'click') {
          this.mbIsLookupModal = true;
        }
      } else {
        this.bmlfOpenErrorBlock('COMMON.MSG.NODATA');
      }
    }, err => {
      console.log(err);
      this.mbIsLoading = false;
    });
  }

  bmlfOpenErrorBlock(psMsg): void {
    this.toastMessageService.notifyError(this.appUtility.bmgfonTranslate(psMsg));
  }

  bmlfSaveLookupField(pobjEvent) {
    
    this.mbIsLookupModal = false;

    if (pobjEvent.field == this.msCaptionForReconcileTypeNo && this.msLookUpKey == "BankAccount") {
      this.mObjModel.ReconcileTypeNo = pobjEvent.value["BMEAcct"].trim();
      this.msBankAcctDescription = pobjEvent.value["BankName"].trim();
    } else if (pobjEvent.field == this.msCaptionForReconcileTypeNo && this.msLookUpKey == "GLAccount") {
      this.mObjModel.ReconcileTypeNo = pobjEvent.value["Acct"].trim();
      this.msBankAcctDescription = pobjEvent.value["Description"].trim();
    } else if (pobjEvent.field == this.msCaptionForReconcileTypeNo && this.msLookUpKey == "CustomerKey") {
      this.mObjModel.ReconcileTypeNo = pobjEvent.value["Customer_Key"].trim();
      this.msBankAcctDescription = pobjEvent.value["Customer_Name"].trim();
    } else if (pobjEvent.field == this.msCaptionForReconcileTypeNo && this.msLookUpKey == "VendorKey") {
      this.mObjModel.ReconcileTypeNo = pobjEvent.value["Vendor_Key"].trim();
      this.msBankAcctDescription = pobjEvent.value["VendorName"].trim();
    } else if (pobjEvent.field == 'Journal No' || pobjEvent.field == 'Finance Series') {
      this.mObjModelGL.Series = pobjEvent.value["Series"].trim();
    }
  }

  bmlfCloseLookupModal(pobjEvent): void {
    this.mbIsLookupModal = pobjEvent.isLookupModal;
    document.getElementById(pobjEvent.eleId).focus();
  }
