  bmlfonValidateFormData() {
    
    if (this.mObjModel.ReconcileDocNo.trim() == "") {
      this.toastMessageService.notifyError((this.appUtility.bmgfonTranslate('TRANRECONCILIATION.Msg.ENTERDOCNO')));
      setTimeout(function () { document.getElementById('txtReconcileDocNo').focus(); }, 10);
      return false;
    }

    if (this.mObjModel.ReconcileTypeNo.trim() == "") {
      this.toastMessageService.notifyError((this.appUtility.bmgfonTranslate('TRANRECONCILIATION.Msg.ENTERACCOUNT')));
      setTimeout(function () { document.getElementById('txtReconcileDocNo').focus(); }, 10);
      return false;
    }

    if (this.marrAfterRecGrid.length == 0 || this.bmlfCheckBlankLine('A') == true) {
      this.toastMessageService.notifyError((this.appUtility.bmgfonTranslate('TRANRECONCILIATION.Msg.NODATA')));
      return false;
    }
    //Validate Field Extender -- Start
    let resultString = this.FieldExtender.bmgfValidationCheck();
    if (resultString.value == false) {
      this.toastMessageService.notifyWarning(resultString.msg);
      return false;
    }
    return true;
  }

