  bmlfGetDataBeforeSave(psId: any) {
    
    let reqData: any;
    let psCustomDateFormat = "dd/MMM/yyyy";
    let dateFrom = moment(this.mObjModel.Fromdate).format(psCustomDateFormat.toUpperCase());
    let dateTo = moment(this.mObjModel.Todate).format(psCustomDateFormat.toUpperCase());
    reqData = {
      JSON: JSON.stringify({
        HeaderData: [{
          ReconciliationNo: this.mObjModel.ReconcileDocNo,
          AccountKey: this.mObjModel.ReconcileTypeNo,
          AccountType: this.msReconcileType,
          FromDate: dateFrom,
          ToDate: dateTo,
        }],
        Response: [{
          "ResponseText": "", "ErrorCode": ""
        }]
      })
    };
    this.objService.bmlfonGetDataForRec(reqData).subscribe(res => {
      if (res == null) {
        this.toastMessageService.notifyError('Server Error');
        return false;
      }
      if (res.Response[0].ErrorCode == "") {
        if (res.Response[0].ResponseText.toUpperCase() == "RECONCILIATION ALREADY EXISTS FOR GL") {
          if (this.mObjModel.ReconcileType == "Bank Accounts") {
            this.toastMessageService.notifyError((this.appUtility.bmgfonTranslate('TRANRECONCILIATION.Msg.BANKACCTEXIST')));
          } else if (this.mObjModel.ReconcileType == "GL Accounts") {
            this.toastMessageService.notifyError((this.appUtility.bmgfonTranslate('TRANRECONCILIATION.Msg.GLACCTEXIST')));
          }
          return false;
        }
        if (res.Response[0].ResponseText.toUpperCase().search('RECONCILIATION NO. ALREADY EXISTS') != -1) {
          this.toastMessageService.notifyError((this.appUtility.bmgfonTranslate('TRANRECONCILIATION.Msg.RECONCILEXIST')));
          return false;
        }
        if (res.BeforeReconciliation.length > 0) {
          this.mObjModelBefore = res.BeforeReconciliation;
          this.marrBeforeRecGrid = res.BeforeReconciliation;
          this.bmlfonInitGridSettings();
          this.marrAfterRecGrid.push(Object.assign({}, this.mObjModelCopyAfter));
          this.bmlfonAfterInitGridSettings();
          this.marrBeforeRecGrid.forEach(ele => {
            ele.AplDate = moment(ele.AplDate).format(this.msCustomDateFormat.toUpperCase());
            ele.TrnAmt = this.appUtility.bmgfSetPrecision(ele.TrnAmt, this.miAmountPrecision);
          });
          this.bmlfonAmtChange();
          this.modalService.open(psId);
          this.bmlfValidateChargesandIncome();
        } else {
          this.marrBeforeRecGrid.push(Object.assign({}, this.mObjModelCopyBefore));
          this.marrAfterRecGrid.push(Object.assign({}, this.mObjModelCopyAfter));
          this.bmlfonInitGridSettings();
          this.bmlfonAfterInitGridSettings();
          this.marrBeforeRecGrid.forEach(ele => {
            ele.AplDate = "";
            ele.TrnAmt = "";
          });
          this.modalService.open(psId);
          this.bmlfValidateChargesandIncome();
        }
        this.marrAfterRecGrid.forEach(ele => {
          ele.AplDate = "";
          ele.TrnAmt = "";
        });
      } else {
        this.toastMessageService.notifyError(res.Response[0].ResponseText);
      }
    });

  }

  bmlfGetDataAfterSave() {
    
    let reqData: any;
    let psCustomDateFormat = "dd/MMM/yyyy";
    let dateFrom = moment(this.mObjModel.Fromdate).format(psCustomDateFormat.toUpperCase());
    let dateTo = moment(this.mObjModel.Todate).format(psCustomDateFormat.toUpperCase());
    reqData = {
      JSON: JSON.stringify({
        HeaderData: [{
          ReconciliationNo: this.mObjModel.ReconcileDocNo,
          AccountKey: this.mObjModel.ReconcileTypeNo,
          AccountType: this.msReconcileType,
          FromDate: dateFrom,
          ToDate: dateTo,
        }],
        Response: [{
          "ResponseText": "", "ErrorCode": ""
        }]
      })
    };
    this.objService.bmlfonGetData(reqData).subscribe(res => {
      if (res.HeaderData.length > 0) {
        this.mObjModel = res.HeaderData[0];
      }
      this.mObjModelBefore = res.BeforeReconciliation;
      this.marrBeforeRecGrid = res.BeforeReconciliation;
      this.mObjModelAfter = res.AfterReconciliation;
      this.marrAfterRecGrid = res.AfterReconciliation;
      this.bmlfonInitGridSettings();
      this.bmlfonAfterInitGridSettings();
      this.RemoveDuplicateRows();
      this.bmlfonAmtChange();
      this.bmlfFormatApplyDate();
    });
  }

  RemoveDuplicateRows() {
    
    let pRowIndex: number = 0;
    let pDuplicateRowIndex = [];
    if (this.marrBeforeRecGrid.length > 0 && this.marrAfterRecGrid.length > 0)
      this.marrBeforeRecGrid.forEach(ele => {
        if (this.blfIsRowExist('A', ele)) {
          pDuplicateRowIndex.push(pRowIndex);
        }
        ++pRowIndex;
      });

    while (pDuplicateRowIndex.length) {
      this.marrBeforeRecGrid.splice(pDuplicateRowIndex.pop(), 1);
    }
  }


  bmlfonAmtChange() {
    
    this.mnumTotalBefore = 0;
    this.mnumTotalAfter = 0;

    this.marrBeforeRecGrid.forEach(ele => {
      if (this.mbIsHome == true) {
        this.mnumTotalBefore = this.mnumTotalBefore + ele.TrnAmt;
      } else {
        this.mnumTotalBefore = this.mnumTotalBefore + ele.NatAmt;
      }
    });
    this.marrAfterRecGrid.forEach(ele => {
      if (this.mbIsHome == true) {
        this.mnumTotalAfter = this.mnumTotalAfter + ele.TrnAmt;
      } else {
        this.mnumTotalAfter = this.mnumTotalAfter + ele.NatAmt;
      }
    });
  }

  bmlfonInitGridSettings() {
    this.marrBeforeRecGridSettings = new GridSettings();
    const parrColumnList = Object.keys(this.marrBeforeRecGrid[0]);
    parrColumnList.forEach(ele => {
      let pobjCol: any = {
        field: ele,
        title: ele,
        filter: '',
        type: 'text',
        filterable: true,
        hidden: false
      }
      if (ele == 'JrnlBtchNo' || ele == 'RecordPtr' || ele == 'ReconcileSrNo' || ele == 'ReconcileFlag' || ele == 'ReconcileDocNo' || ele == 'Flag') {
        pobjCol.hidden = true;
      }
      if (this.mbIsHome == true && (ele == 'CurrencyKey' || ele == 'NatAmt')) {
        pobjCol.hidden = true;
      }
      if (this.mbIsHome == false && ele == 'TrnAmt') {
        pobjCol.hidden = true;
      }
      if (ele == 'ParentdocNo') {
        pobjCol.title = "CHECKNO";
      }

      pobjCol.title = this.appUtility.bmgfonTranslate("TRANRECONCILIATION." + pobjCol.title.toUpperCase() + "");

      this.marrBeforeRecGridSettings.columnsConfig.push(pobjCol);
    });
  }

  bmlfonAfterInitGridSettings() {
    this.marrAfterRecGridSettings = new GridSettings();
    const parrColumnList = Object.keys(this.marrAfterRecGrid[0]);
    parrColumnList.forEach(ele => {
      let pobjCol: any = {
        field: ele,
        title: ele,
        filter: '',
        type: 'text',
        filterable: true,
        hidden: false
      }
      if (ele == 'JrnlBtchNo' || ele == 'RecordPtr' || ele == 'ReconcileFlag' || ele == 'ReconcileDocNo' || ele == 'Flag') {
        pobjCol.hidden = true;
      }
      if (this.mbIsHome == true && (ele == 'CurrencyKey' || ele == 'NatAmt')) {
        pobjCol.hidden = true;
      }
      if (this.mbIsHome == false && ele == 'TrnAmt') {
        pobjCol.hidden = true;
      }
      if (ele == 'ParentdocNo') {
        pobjCol.title = "CHECKNO";
      }
      if (ele == 'ReconcileSrNo') {
        pobjCol.title = "SrNo";
      }

      pobjCol.title = this.appUtility.bmgfonTranslate("TRANRECONCILIATION." + pobjCol.title.toUpperCase() + "");

      this.marrAfterRecGridSettings.columnsConfig.push(pobjCol);
    });
  }

