  bmlfDeleteBlankLine(type) {
    let piRowindex: number = 0
    if (type == "B") {
      this.marrBeforeRecGrid.forEach(ele => {
        if (ele.Acct == "") {
          this.marrBeforeRecGrid.splice(piRowindex, 1);
        }
        ++piRowindex;
      });
    } else if (type == "A") {
      this.marrAfterRecGrid.forEach(ele => {
        if (ele.Acct == "") {
          this.marrAfterRecGrid.splice(piRowindex, 1);
        }
        ++piRowindex;
      });
    }
  }

  bmlfonReconciliationProcess() {
    
    //check Write permission
    if (!this.appUtility.mbWrite) {
      this.toastMessageService.notifyWarning(this.appUtility.bmgfonTranslate('COMMON.PERMISSION_WRITE'));
      return;
    };
    if (this.marrBeforeRecGrid.length == 1) {
      this.marrBeforeRecGrid.forEach(ele => {
        if (ele.Acct == "") {
          ele.AplDate = new Date();
          ele.TrnAmt = "0";
        }
      });
    }

    let parrBeforeRecGrid = this.marrBeforeRecGrid.map(ele => Object.assign({}, ele));
      parrBeforeRecGrid.map(ele => {
        ele.TrnAmt = this.appUtility.bmgfSetPrecision(ele.TrnAmt, this.miPrecision);
        ele.NatAmt = this.appUtility.bmgfSetPrecision(ele.NatAmt, this.miPrecision);     
      });
      let parrAfterRecGrid = this.marrAfterRecGrid.map(ele => Object.assign({}, ele));
      parrAfterRecGrid.map(ele => {
        ele.TrnAmt = this.appUtility.bmgfSetPrecision(ele.TrnAmt, this.miPrecision);
        ele.NatAmt = this.appUtility.bmgfSetPrecision(ele.NatAmt, this.miPrecision);     
      });

    let reqData: any = {
      JSON: JSON.stringify({
        HeaderData: [{ ...this.mObjModel }],
        BeforeReconciliation: parrBeforeRecGrid,
        AfterReconciliation: parrAfterRecGrid,
        AfterExcelReconciliation: [{ ...this.mObjModelAfterExcel }],
        ExcelMapping: [{ ...this.mObjModelExcelMapping }],
        Response: [{
          "ResponseText": "", "ErrorCode": ""
        }]
      })
    }
    this.objService.bmlfonProcessData(reqData).subscribe(res => {
      if (res == null) {
        this.toastMessageService.notifyError('Server Error');
        return false;
      }
      if (res.Response[0].ErrorCode == "") {
        this.marrAfterRecGrid = [];
        this.bmlfDeleteBlankLine('B');
        this.msShowDialog = this.appUtility.bmgfonTranslate(res.Response[0].ResponseText);
        if (this.msShowDialog.length < this.mnumMessagelen) {
          this.toastMessageService.notifySuccess(res.Response[0].ResponseText);
        } else {
          this.mbShowYesNo = false;
          // this.modalService.open('SaveChangesMessageModal');
          this.mbReportDialog =true;
        }
      } else {
        this.toastMessageService.notifyError(res.Response[0].ResponseText);
      }
    });
  }

  bmlfonTranDetailProcess() {
    

    if (this.marrAfterRecGrid.length == 0 || this.bmlfCheckBlankLine('A')) {
      this.toastMessageService.notifyError((this.appUtility.bmgfonTranslate('TRANRECONCILIATION.Msg.NOPROCESSDATA')));
      return;
    }
    if (this.marrBeforeRecGrid.length == 0) {
      this.marrBeforeRecGrid.push(Object.assign({}, this.mObjModelCopyBefore));
      this.bmlfonInitGridSettings();
      this.marrBeforeRecGrid.forEach(ele => {
        ele.AplDate = "";
        ele.TrnAmt = "";
      });

    }
    this.msShowDialog = this.appUtility.bmgfonTranslate('TRANRECONCILIATION.Msg.BOOKDIFFERENCE');
    this.mbShowYesNo = true;
    this.mbReportDialog =true;
    //this.modalService.open('SaveChangesMessageModal');
  }

