      parrFilterData = this.marrBeforeRecGrid.filter(obj => {
        if ((obj[psInputKey].toUpperCase() == dataItem.JrnlBtchNo.toUpperCase()) && (obj['Description'].toUpperCase() == dataItem.Description.toUpperCase())) {
          pbResult = true;
        }
      });
      // this.marrBeforeRecGrid.forEach(ele => {
      //   if (ele.JrnlBtchNo == JrnlBtchNo && pbResult == false) {
      //     pbResult = true;
