  bmlfClearCharges() {
    this.mObjModelGL.Series = "";
    this.mObjModelGL.Amount = 0;
  }

  bmlfRefreshBeforeRecociliationGrid() {
    
    let reqData: any;
    let psCustomDateFormat = "dd/MMM/yyyy";
    let dateFrom = moment(this.mObjModel.Fromdate).format(psCustomDateFormat.toUpperCase());
    let dateTo = moment(this.mObjModel.Todate).format(psCustomDateFormat.toUpperCase());
    reqData = {
      JSON: JSON.stringify({
        HeaderData: [{
          ReconciliationNo: this.mObjModel.ReconcileDocNo,
          AccountKey: this.mObjModel.ReconcileTypeNo,
          AccountType: this.msReconcileType,
          FromDate: dateFrom,
          ToDate: dateTo,
        }],
        Response: [{
          "ResponseText": "", "ErrorCode": ""
        }]
      })
    };
    this.objService.bmlfonGetDataForRec(reqData).subscribe(res => {
      if (res == null) {
        this.toastMessageService.notifyError('Server Error');
        this.mbGLProcess = false;
        return false;
      }
      if (res.Response[0].ErrorCode == "") {

        if (res.BeforeReconciliation.length > 0) {
          this.mObjModelBefore = res.BeforeReconciliation;
          this.marrBeforeRecGrid = res.BeforeReconciliation;
          this.bmlfonInitGridSettings();
          this.marrBeforeRecGrid.forEach(ele => {
            ele.AplDate = moment(ele.AplDate).format(this.msCustomDateFormat.toUpperCase());
          });
          this.bmlfonAmtChange();
          if (this.mbGLProcess == true) {
            this.RemoveDuplicateRows();
          }
        }
        else {
          this.mbGLProcess = false;
        }
      } else {
        this.mbGLProcess = false;
        this.toastMessageService.notifyError(res.Response[0].ResponseText);
      }
    });
  }

  public bmlfValidateChargesandIncome(): void {
    if (this.mObjModel.ReconcileType != "Bank Accounts") {
      return;
    }
    this.mbBankChargesValidate = true;
    this.mbOtherChargesValidate = true;
    this.mbInterestIncomeValidate = true;
    this.mbOtherIncomeValidate = true;
    let pobjReqData = {
      ScreenName: 'TransactionReconcileComponent',
      LookUpKey: 'AccountDetails',
      Filter1: this.mObjModel.ReconcileTypeNo
    } 
    this.userService.GETGeneralSqlData(pobjReqData).subscribe(res => {
      if (res.LookupData.length > 0) {
        if (res.LookupData[0].BankChrgAcct == "") {
          this.mbBankChargesValidate = false;
        }
        if (res.LookupData[0].OtherChrgAcct == "") {
          this.mbOtherChargesValidate = false;
        }
        if (res.LookupData[0].InterestIncmAcct == "") {
          this.mbInterestIncomeValidate = false;
        }
        if (res.LookupData[0].OtherIncmAcct == "") {
          this.mbOtherIncomeValidate = false;
        }
      }
    });
  }

