  bmlfonGLEntryOk(psId: any, action: any) {
    
    let pbResult: boolean = false;
    pbResult = this.bmlfonValidateGLData();
    if (pbResult == true) {
      this.mObjModelGL.ReconcileDocNo = this.mObjModel.ReconcileDocNo;
      this.mObjModelGL.AccountKey = this.mObjModel.ReconcileTypeNo;
      this.bmlfonGLProcess();
      if (action == 'close') {
        this.modalService.close(psId);
      }
    }
  }


  bmlfonGLProcess() {
    
    let psCustomDateFormat = "dd/MMM/yyyy";
    let dateGL = moment(this.mObjModelGL.Date).format(psCustomDateFormat.toUpperCase());
    let reqData: any = {
      JSON: JSON.stringify({
        HeaderData: [{
          ReconcileDocNo: this.mObjModelGL.ReconcileDocNo,
          AccountKey: this.mObjModelGL.AccountKey,
          CallType: this.mObjModelGL.CallType,
          Series: this.mObjModelGL.Series,
          Date: dateGL,
          Amount: this.mObjModelGL.Amount,
          Narration: this.mObjModelGL.Narration,
        }],
        Response: [{
          "ResponseText": "", "ErrorCode": ""
        }]
      })
    }
    this.objService.bmlfonGLProcessData(reqData).subscribe(res => {
      if (res == null) {
        this.toastMessageService.notifyError('Server Error');
        return false;
      }
      if (res.Response[0].ErrorCode == "") {
        if (this.msSaved == 'true') {
          this.bmlfGetDataAfterSave();
        }
        else {
          this.mbGLProcess = true;
          this.bmlfRefreshBeforeRecociliationGrid();
        }
        this.msShowDialog = this.appUtility.bmgfonTranslate(res.Response[0].ResponseText);
        if (this.msShowDialog.length > 0) {
          this.mbShowYesNo = false;
          this.mbReportDialog =true;
          //this.modalService.open('SaveChangesMessageModal');
        }
      } else {
        this.toastMessageService.notifyError(res.Response[0].ResponseText);
      }
    });
  }

  bmlfonGLEntryClose(psId: any, action: any) {
    if (action == 'close') {
      this.modalService.close(psId);
    }
  }

  bmlfonValidateGLData() {
    
    if (this.mObjModelGL.Series.trim() == "") {
      this.toastMessageService.notifyError((this.appUtility.bmgfonTranslate('TRANRECONCILIATION.Msg.EMPTYSERIES')));
      setTimeout(function () { document.getElementById('txtGLSeries').focus(); }, 10);
      return false;
    } else if (this.mObjModelGL.Amount == 0) {
      this.toastMessageService.notifyError((this.appUtility.bmgfonTranslate('TRANRECONCILIATION.Msg.GLAMOUNT')));
      setTimeout(function () { document.getElementById('txtGLAmount').focus(); }, 10);
      return false;
    }
    return true;
  }

  bmlfOpenLookupSQLModel(psField, psScreenName, psLookUpKey, psEvent, psValue, psType, psEleId, pnumRowIdx = null) {
    
    this.marrLookupGridData = [];
    this.mobjLookupKeyData = {};
    if (psEvent == 'blur' && (psValue == undefined || psValue == '')) {
      this.mobjLookupDataModel[psField] = "";
      return false;
    }
    let reqData: any = {
      JSON: JSON.stringify({
        HeaderData: [{
          ScreenName: "Journal Entry",
        }],
        Response: [{
          "ResponseText": "", "ErrorCode": ""
        }]
      })
    };

    this.objService.bmlfonGetFinanceSeries(reqData).subscribe(res => {
      if (res.SeriesData.length > 0) {
        this.marrLookupGridData = res.SeriesData;

        this.mobjLookupKeyData.field = 'Finance Series';
        //this.mobjLookupKeyData.field = psField;
        this.mobjLookupKeyData.gridData = this.marrLookupGridData;
        this.mobjLookupKeyData.type = psType;
        this.mobjLookupKeyData.eleId = psEleId;

        this.mobjLookupKeyData.gridData = this.marrLookupGridData;
        if (psEvent == 'blur' && psValue) {
          let parrFilterdArr: any = [];
          let pobjFirstInput = Object.keys(this.marrLookupGridData[0])[0];
          parrFilterdArr = this.marrLookupGridData.filter(obj => {
            if (obj[pobjFirstInput].toUpperCase() == psValue.toUpperCase()) {
              return obj;
            }
          });
          if (parrFilterdArr.length == 1) {
            this.mobjLookupKeyData.value = parrFilterdArr[0];
            this.mobjLookupKeyData.valueKey = parrFilterdArr[0][pobjFirstInput];
            this.bmlfSaveLookupField(this.mobjLookupKeyData);
            this.mbIsLookupModal = false;
          } else {
            this.mbIsLookupModal = true;
          }
        }
        if (psEvent == 'click') {
          this.mbIsLookupModal = true;
        }
      }
    });
  }

