  bmlfonDeleteDialogAction(action) {
    
    if (action == 'No') {
      this.mbShowDeleteDialog = false;
      setTimeout(() => {
      this.modalService.open('TransactionDetail');
        
      }, 100);

    } else if (action == 'Yes') {
      // if (this.msFormMode == "I" && (this.mbChange == true || this.FieldExtender.mbChange == true || this.customCtrl.mbChange == true)) {
      //   this.mbShowDeleteDialog = false;
      //   this.bmlfonClearControl();
      //   return;
      // } else if (this.mbInsert && this.mbChange) {/

      this.mbShowDeleteDialog = false;
      let deleteKey = this.appUtility.bmgfonTranslate('CUSTOMERITEM.MSG.DELETE') + '-' + this.mObjModel.ReconcileDocNo;
      this.FieldExtender.bmgfDeleteData(deleteKey);
      let reqData: any = {
        JSON: JSON.stringify({
          HeaderData: [{
            ReconcileDocNo: this.mObjModel.ReconcileDocNo,
          }],
          Response: [{
            "ResponseText": "", "ErrorCode": ""
          }]
        })
      };
      this.objService.bmlfonDeleteData(reqData).subscribe(res => {
        if (res == null) {
          this.toastMessageService.notifyError('Server Error');
          return false;
        }
        if (res.Response[0].ErrorCode == "") {
          {
            if (res.Response[0].ResponseText.toUpperCase().search('DELETED') != -1) {
              this.toastMessageService.notifySuccess((this.appUtility.bmgfonTranslate('COMMON.MSG.DELETE')));
              this.mbShowDeleteDialog = false;
              this.modalService.open('TransactionDetail');
              this.router.navigate([this.msRoutePath]);
            } else {
              this.toastMessageService.notifyError(res.Response[0].ResponseText);
              this.mbShowDeleteDialog = false;
              this.modalService.open('TransactionDetail');
              this.router.navigate([this.msRoutePath]);
            }
          }
        }
      });
      this.bmlfonClearControl();
      // this.toastMessageService.notifyWarning(this.appUtility.bmgfonTranslate('COMMON.MSG.DELETE'));
      return false;
    }

    // }
  }


  bmlfonFromChange(value: Date) {
    let pbResult: boolean = true;
    pbResult = CustomValidator.bmgfCompareTwoDates(value, this.mObjModel.Todate);
    if (pbResult == false) {
      this.toastMessageService.notifyError((this.appUtility.bmgfonTranslate('TRANRECONCILIATION.Msg.VALIDFROMDATE')))
      this.mObjModel.Fromdate = new Date(); //this.bmlfonGenerateIndentFromData();
      return false;
    }
  }

  bmlfonToChange(value: Date) {
    let pbResult: boolean = true;
    pbResult = CustomValidator.bmgfCompareTwoDates(this.mObjModel.Fromdate, value);
    if (pbResult == false) {
      this.toastMessageService.notifyError((this.appUtility.bmgfonTranslate('TRANRECONCILIATION.Msg.VALIDTODATE')))
      this.mObjModel.Todate = this.mObjModel.Fromdate;
      return false;
    }
  }

  //Open Task MODAL 
  bmlfOpenTaskModal(taskType) {
    this.mobjTaskData = {};
    this.mobjTaskData.taskType = taskType;
    if (taskType == 'myTask') {
      this.mobjTaskData.title = this.appUtility.bmgfonTranslate('MYTASK');
      this.mobjTaskData.innerRecord = this.mobjUserData;
    } else if (taskType == 'recordTask') {
      this.mobjTaskData.title = this.appUtility.bmgfonTranslate('TASKFORRECORD');
      let pobjFormRec: any = {};
      pobjFormRec.formName = this.msScreenName;
      pobjFormRec.primeKey = this.mObjModel.ReconcileDocNo;
      this.mobjTaskData.innerRecord = pobjFormRec;
    }
    this.mbIsTaskModal = true;
  }

  //Close Task MODAL 
  bmlfonCloseTaskModal(pobjData) {
    this.mbIsTaskModal = pobjData.isTaskModal;
  }

  SetGridColumns(GridName, parrGridCol) {
    this.appUtility.GetJsonSetting('Transaction Reconciliation Details', this.mobjUserData.UserId, GridName, parrGridCol).then((pArrGridChangedCol: any) => {
      if (pArrGridChangedCol != null && pArrGridChangedCol != undefined) {
        parrGridCol = pArrGridChangedCol
      } 
    }, err => {
      console.log(err);     
    });
  }
