import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { Location } from '@angular/common';
import { ActivatedRoute, Router } from '@angular/router';
import { AlertService } from '../../../../../../core/services/alert.service';
import { ToastMessageService } from '../../../../../../core/services/toast-message.service';
import { AppUtility } from '../../../../../../core/services/utility.services';
import { LookupService } from '../../../../../../shared/http/lookup.service';
import { GridSettings } from 'src/app/core/models/grid-settings.interface';
import { TransactionReconcilationModel, BeforeReconciliation, AfterReconciliation, AfterExcelReconciliation, ExcelMapping } from '../../../../models/transaction-reconcilation.model';
import { TransactionReconcilationService } from '../../../../http/transaction-reconcilation.service';
import { timeout } from 'rxjs/operators';
import * as moment from 'moment';
import { DateFormatService } from 'src/app/core/services/dateFormat.service';
import { CustomValidator } from 'src/app/core/services/custom.validators';
import { UserFieldService } from 'src/app/shared/http/userfield.service';
import { LocalStorageService } from 'src/app/core/services/local-storage.service';

@Component({
  selector: 'app-transaction-reconcilation-manageedit',
  templateUrl: './transaction-reconcilation-manageedit.component.html',
  styleUrls: ['./transaction-reconcilation-manageedit.component.scss']
})
export class TransactionReconcilationManageeditComponent implements OnInit {
  public mObjModel: TransactionReconcilationModel = new TransactionReconcilationModel();
  public mObjModelBefore: BeforeReconciliation = new BeforeReconciliation();
  public mObjModelAfter: AfterReconciliation = new AfterReconciliation();
  public mObjModelAfterExcel: AfterExcelReconciliation = new AfterExcelReconciliation();
  public mObjModelExcelMapping: ExcelMapping = new ExcelMapping();

  public msScreenName: string = "Transaction Reconciliation";
  public msRoutePath: string = '/main/bankcash/transaction-reconciliation';
  public msRoutePathManage: string = '/main/bankcash/transaction-reconciliation/manage';
  public msRoutePathManageEdit: string = '/main/bankcash/transaction-reconciliation/manageedit';

  public marrBeforeRecGrid = [];
  public marrAfterRecGrid = [];
  public marrBeforeRecGridSettings: GridSettings = new GridSettings();
  public marrAfterRecGridSettings: GridSettings = new GridSettings();

  public msSaved: string = '';
  public msReconcileDocNo: string = '';
  public msReconcileType: string = '';
  public msReconcileTypeNo: string = '';
  public msCurrency: string = '';
  public msFromdate: string = '';
  public msTodate: string = '';
  public mnumStatmentBal: Number = 0;
  public msFileName: string = '';

  public mnumTotalBefore: number = 0;
  public mnumTotalAfter: number = 0;

  public mbShowDialog: boolean = false;

  constructor(
    private location: Location,
    private objService: TransactionReconcilationService,
    private activeRoute: ActivatedRoute,
    private toastMessageService: ToastMessageService,
    private appUtility: AppUtility,
    private objLookupService: LookupService,
    private router: Router,
    private userService: UserFieldService,
    private dateFormatService: DateFormatService,
  ) { }

  ngOnInit() {
    
    this.msSaved = this.activeRoute.snapshot.queryParamMap.get("Saved") == null ? '' : this.activeRoute.snapshot.queryParamMap.get("Saved");
    this.msReconcileDocNo = this.activeRoute.snapshot.queryParamMap.get("ReconciliationNo") == null ? '' : this.activeRoute.snapshot.queryParamMap.get("ReconciliationNo");
    this.mObjModel.ReconcileDocNo = this.msReconcileDocNo;
    this.msReconcileTypeNo = this.activeRoute.snapshot.queryParamMap.get("AccountKey") == null ? '' : this.activeRoute.snapshot.queryParamMap.get("AccountKey");
    this.mObjModel.ReconcileTypeNo = this.msReconcileTypeNo;
    this.msReconcileType = this.activeRoute.snapshot.queryParamMap.get("AccountType") == null ? '' : this.activeRoute.snapshot.queryParamMap.get("AccountType");
    this.mObjModel.ReconcileType = this.msReconcileType;
    this.msFromdate = this.activeRoute.snapshot.queryParamMap.get("FromDate") == null ? '' : this.activeRoute.snapshot.queryParamMap.get("FromDate");
    this.mObjModel.Fromdate = new Date(this.msFromdate);
    this.msTodate = this.activeRoute.snapshot.queryParamMap.get("ToDate") == null ? '' : this.activeRoute.snapshot.queryParamMap.get("ToDate");
    this.mObjModel.Todate = new Date(this.msTodate);

    if (this.msSaved == 'true') {
      this.bmlfGetDataAfterSave();
    } else if (this.msSaved == 'false') {
      this.bmlfGetDataBeforeSave();
    }

  }

  bmlfGetDataBeforeSave() {
    
    let reqData: any;

    reqData = {
      JSON: JSON.stringify({
        HeaderData: [{
          ReconciliationNo: this.mObjModel.ReconcileDocNo,
          AccountKey: this.mObjModel.ReconcileTypeNo,
          AccountType: this.mObjModel.ReconcileType,
          FromDate: this.mObjModel.Fromdate,
          ToDate: this.mObjModel.Todate,
        }],
        Response: [{
          "ResponseText": "", "ErrorCode": ""
        }]
      })
    };
    this.objService.bmlfonGetDataForRec(reqData).subscribe(res => {
      if (res.HeaderData.length > 0) {
        this.mObjModel = res.HeaderData[0];
      }
      this.mObjModelBefore = res.BeforeReconciliation;
      this.marrBeforeRecGrid = res.BeforeReconciliation;
      this.bmlfonInitGridSettings();
      this.bmlfonAmtChange();
    });
  }

  bmlfGetDataAfterSave() {
    
    let reqData: any;

    reqData = {
      JSON: JSON.stringify({
        HeaderData: [{
          ReconciliationNo: this.mObjModel.ReconcileDocNo,
          AccountKey: this.mObjModel.ReconcileTypeNo,
          AccountType: this.mObjModel.ReconcileType,
          FromDate: this.mObjModel.Fromdate,
          ToDate: this.mObjModel.Todate,
        }],
        Response: [{
          "ResponseText": "", "ErrorCode": ""
        }]
      })
    };
    this.objService.bmlfonGetData(reqData).subscribe(res => {
      if (res.HeaderData.length > 0) {
        this.mObjModel = res.HeaderData[0];
      }
      this.mObjModelBefore = res.BeforeReconciliation;
      this.marrBeforeRecGrid = res.BeforeReconciliation;
      this.mObjModelAfter = res.AfterReconciliation;
      this.marrAfterRecGrid = res.AfterReconciliation;
      this.bmlfonInitGridSettings();
      this.bmlfonAmtChange();
    });
  }

  bmlfonSubmitData() {
    ;
    //check Write permission
    // if (!this.appUtility.mbWrite) {
    //   this.toastMessageService.notifyWarning(this.appUtility.bmgfonTranslate('COMMON.PERMISSION_WRITE'));
    //   return;
    // };

    let pbResult: Boolean;
    pbResult = this.bmlfonValidateFormData();
    if (pbResult) {
      // let data = this.appUtility.bmgfCustomModelTransform(this.customCtrl);
      //this.bmlfonAssignCustValues(data);
      this.mObjModel.Status = 'Open';
      let reqData: any = {
        JSON: JSON.stringify({
          HeaderData: [{ ...this.mObjModel }],
          BeforeReconciliation: this.marrBeforeRecGrid,
          AfterReconciliation: this.marrAfterRecGrid,
          AfterExcelReconciliation: [{ ...this.mObjModelAfterExcel }],
          ExcelMapping: [{ ...this.mObjModelExcelMapping }],
          Response: [{
            "ResponseText": "", "ErrorCode": ""
          }]
        })
      }
      this.objService.bmlfonSaveData(reqData).subscribe(res => {
        if (res == null) {
          this.toastMessageService.notifyError('Server Error');
          return false;
        }
        if (res.Response[0].ErrorCode == "") {
          this.toastMessageService.notifySuccess(this.appUtility.bmgfonTranslate('COMMON.SAVED'));

        } else {
          this.toastMessageService.notifyError(res.Response[0].ResponseText);
        }
      });
    }
  }

  bmlfonProcess() {
    ;

    let reqData: any = {
      JSON: JSON.stringify({
        HeaderData: [{ ...this.mObjModel }],
        BeforeReconciliation: this.marrBeforeRecGrid,
        AfterReconciliation: this.marrAfterRecGrid,
        AfterExcelReconciliation: [{ ...this.mObjModelAfterExcel }],
        ExcelMapping: [{ ...this.mObjModelExcelMapping }],
        Response: [{
          "ResponseText": "", "ErrorCode": ""
        }]
      })
    }
    this.objService.bmlfonProcessData(reqData).subscribe(res => {
      if (res == null) {
        this.toastMessageService.notifyError('Server Error');
        return false;
      }
      if (res.Response[0].ErrorCode == "") {
        this.toastMessageService.notifySuccess(res.Response[0].ResponseText);

      } else {
        this.toastMessageService.notifyError(res.Response[0].ResponseText);
      }
    });
  }


  bmlfonValidateFormData() {
    ;
    if (this.msReconcileDocNo == "") {
      this.toastMessageService.notifyError((this.appUtility.bmgfonTranslate('TRANRECONCILIATION.Msg.ENTERDOCNO')));
      return false;
    }

    if (this.msReconcileTypeNo == "") {
      this.toastMessageService.notifyError((this.appUtility.bmgfonTranslate('TRANRECONCILIATION.Msg.ENTERACCOUNT')));
      return false;
    }
    return true;
  }


  bmlfonAmtChange() {
    
    this.mnumTotalBefore = 0;
    this.mnumTotalAfter = 0;

    this.marrBeforeRecGrid.forEach(ele => {
      this.mnumTotalBefore = this.mnumTotalBefore + ele.TrnAmt;
    });
    this.marrAfterRecGrid.forEach(ele => {
      this.mnumTotalAfter = this.mnumTotalAfter + ele.TrnAmt;
    });
  }

  bmlfonInitGridSettings() {
    const parrColumnList = Object.keys(this.marrBeforeRecGrid[0]);
    parrColumnList.forEach(ele => {
      let pobjCol: any = {
        field: ele,
        title: ele,
        filter: '',
        type: 'text',
        filterable: true,
        hidden: false
      }
      if (ele == 'JrnlBtchNo' || ele == 'RecordPtr' || ele == 'NatAmt' || ele == 'ReconcileFlag' || ele == 'CurrencyKey' || ele == 'ParentdocNo' || ele == 'ReconcileDocNo' || ele == 'ReconcileSrNo' || ele == 'Flag') {
        pobjCol.hidden = true;
      }

      pobjCol.title = this.appUtility.bmgfonTranslate("TRANRECONCILIATION." + pobjCol.title.toUpperCase() + "");

      this.marrBeforeRecGridSettings.columnsConfig.push(pobjCol);
    });
  }

  bmlfonDelete() {
    let reqData: any = {
      JSON: JSON.stringify({
        HeaderData: [{
          ReconcileDocNo: this.mObjModel.ReconcileDocNo,
        }],
        Response: [{
          "ResponseText": "", "ErrorCode": ""
        }]
      })
    };
    this.objService.bmlfonDeleteData(reqData).subscribe(res => {
      if (res == null) {
        this.toastMessageService.notifyError('Server Error');
        return false;
      }
      if (res.Response[0].ErrorCode == "") {
        {
          if (res.Response[0].ResponseText.toUpperCase().search('DELETED') != -1) {
            this.toastMessageService.notifySuccess((this.appUtility.bmgfonTranslate('COMMON.MSG.DELETE')));
            this.router.navigate([this.msRoutePath]);
          } else {
            this.toastMessageService.notifyError(res.Response[0].ResponseText);
            this.router.navigate([this.msRoutePath]);
          }
        }
      } else {
        this.toastMessageService.notifyError(res.Response[0].ResponseText);
      }
    });
  }

  bmlfonSave() {
    this.bmlfonSubmitData();
  }

  bmlfonClose() {
    this.router.navigate([this.msRoutePath]);
  }
}
