  // Add Line Item Grid 1
  bmlfonAddLineToGrid() {

    if (!this.appUtility.mbWrite) {
      this.toastMessageService.notifyWarning(this.appUtility.bmgfonTranslate('COMMON.PERMISSION_WRITE'));
      return false;
    };
    if (this.mObjModel.BankID == undefined || this.mObjModel.BankID == 0) {
      this.toastMessageService.notifyError((this.appUtility.bmgfonTranslate('CheckBookMaster.Msg.Key')));
      return false;
    };
    const pobjDataCopy: any = Object.assign({}, this.mObjCheckBookModelCopy);
    let piLastIndex;
    piLastIndex = this.mArrGrid.length;
    if (piLastIndex > 0) {
      let index = this.mArrGrid.length - 1;
      if (this.isEmpty(this.mArrGrid[index]['ChqBookID'])) {
        this.toastMessageService.notifyError((this.appUtility.bmgfonTranslate('CheckBookMaster.Msg.Key4')));
        let eleID = 'txtChqBookID' + (index).toString();
        setTimeout(function () { document.getElementById(eleID).focus(); }, 10);
      } else {
        pobjDataCopy.Flag = "A";
        pobjDataCopy.BankID = this.mObjModel.BankID;
        pobjDataCopy.SeqNo = this.mArrGrid.length + 1;;
        pobjDataCopy.Recuserid = this.msUserId;
        pobjDataCopy.CreateUserID = this.msUserId;
        pobjDataCopy.ModifyUserID = this.msUserId;
        this.mArrGrid.push(pobjDataCopy);
        let eleID = 'txtChqBookID' + (piLastIndex).toString();
        setTimeout(() => { document.getElementById(eleID).focus(); }, 10);
      }
    } else {
      pobjDataCopy.Flag = "A";
      pobjDataCopy.BankID = this.mObjModel.BankID;
      pobjDataCopy.SeqNo = 1;
      pobjDataCopy.Recuserid = this.msUserId;
      pobjDataCopy.CreateUserID = this.msUserId;
      pobjDataCopy.ModifyUserID = this.msUserId;
      this.mArrGrid.push(pobjDataCopy);
      let eleID = 'txtChqBookID' + (piLastIndex).toString();
      setTimeout(() => { document.getElementById(eleID).focus(); }, 10);
    }
  }

