  bmlfonDeleteGrid1Line(piSeqNo: any) {

    let psChqBookId: string = "";
    if (this.mbShowOnlyActiveCheck) {
      psChqBookId = this.mArrGrid[this.miGrid1DeleteLineIndex].ChqBookID
    } else {
      psChqBookId = this.mArrGrid[this.miGrid1DeleteLineIndex].ChqBookID
    }

    let pSrequestObj: any = {
      HeaderData: [{
        BankID: this.mObjModel.BankID,
        ChqBookID: this.mArrGrid[this.miGrid1DeleteLineIndex].ChqBookID
      }],
      Response: [{ ResponseText: '', 'ErrorCode': '' }]
    };
    let reqData: any = {
      JSON: JSON.stringify(pSrequestObj)
    };
    // Call to Delete Line Method for Cash Book Entry
    this.objService.bmlfonDeleteChequeBookRecord(reqData).subscribe(res => {

      if (res == null) { return false; }
      if (!this.isEmpty(res.Response[0].ErrorCode)) {
        this.toastMessageService.notifyError(res.Response[0].ResponseText);
      } else if (res.Response[0].ResponseText.toUpperCase().search('ALREADY USED') != -1) {
        let str: string = res.Response[0].ResponseText;
        str = str.replace(/<br>/g, "")
        this.toastMessageService.notifyError(str);
      }
      else {
        this.toastMessageService.notifySuccess(this.appUtility.bmgfonTranslate('COMMON.MSG.LINEDELETE'));
        this.mArrGrid_AllData.splice(this.miGrid1DeleteLineIndex, 1);
        this.bmlfGetCheckBookData(this.mObjModel.BankID);
      }
    });
  }

  bmlfGetCheckBookData(psKey) {

    this.mArrGrid = [];
    this.mArrGrid_AllData = [];
    this.mObjCheckBookModel = Object.assign({}, this.mObjCheckBookModelCopy);
    let reqData: any = {
      JSON: JSON.stringify({
        HeaderData: [{ BankID: psKey }],
        Response: [{ "ResponseText": "", "ErrorCode": "" }]
      })
    };
    this.objService.bmlfonGetChequeBookData(reqData).subscribe(res => {

      if (res.HeaderData.length > 0) {

        // this.mArrGrid_AllData = res.ChqBookData;
        let parrGridData = res.ChqBookData;
        parrGridData.forEach(ele => {
          ele.ChqBookIssueDate = new Date(ele.ChqBookIssueDate);
          // if (this.mbShowOnlyActiveCheck) {
         
          // // if (ele.ChqBookActiveInActive.toString() == "true") {
          // //   this.mArrGrid.push(ele);
          // // Bug 188569 }
          ////////////////////////////////
            if (ele.ChqBookActiveInActive.toString() == "true") {
              ele.ChqBookActiveInActive = true;
            }
            else if(ele.ChqBookActiveInActive.toString() == "false"){
              ele.ChqBookActiveInActive = false;
            } 
          /////////////////////////
            this.mArrGrid.push(ele); 
          // } else {
          //   this.mArrGrid.push(ele);
          // }
        });
        this.mArrGrid_AllData = parrGridData;
      }
    });
  }

  public bmlfSaveCheckBook(): void {

    //check Write permission
    if (!this.appUtility.mbWrite) {
      this.toastMessageService.notifyWarning(this.appUtility.bmgfonTranslate('COMMON.PERMISSION_WRITE'));
      return;
    };

    if (!this.mbChangeCheckBoook) {
      return;
    };

    let pbResult: Boolean = false;
    pbResult = this.bmlfOnValidateFromCheckBookData();
    //////Bug 188569//////
    this.mArrGrid.forEach(ele =>{
      if(ele.ChqBookActiveInActive == true){
        ele.ChqBookActiveInActive = "true"
      }
      else if(ele.ChqBookActiveInActive == false){
        ele.ChqBookActiveInActive = "false"
      }
    });
    ////////////////////////////
    if (pbResult) {
      let reqData: any = {
        JSON: JSON.stringify({
          ChqBookData: this.mArrGrid,
          Response: [{
            "ResponseText": "", "ErrorCode": ""
          }]
        })
      };
      this.objService.bmlfonSaveChequeBookData(reqData).subscribe(res => {
        if (res == null) {
          this.toastMessageService.notifyError('Server Error');
          return false;
        }
        if (res.Response[0].ErrorCode == "") {
          {
            this.toastMessageService.notifySuccess(this.appUtility.bmgfonTranslate('COMMON.SAVED'));
            this.bmlfGetCheckBookData(this.mObjModel.BankID);
            this.mbChangeCheckBoook = false;
          }
        } else {
          if (this.appUtility.bmgffindString(res.Response[0].ResponseText, 'EXIST')) {
            this.toastMessageService.notifyError(this.appUtility.bmgfonTranslate('COMMON.MSG.EXIST'));
          } else {
            this.toastMessageService.notifyError(res.Response[0].ResponseText);
          }
          this.mbShowDialog = false;
        }
      });
    }
    this.mbShowDialog = false;
  }

  bmlfNgChanges(piRowId, psField) {

    this.mbChangeCheckBoook = true;
    if (this.mArrGrid[piRowId].Flag != "A") {
      this.mArrGrid[piRowId].Flag = "M";
    }
    if (psField == "StartChqNo" && Number(this.mArrGrid[piRowId]['StartChqNo']) > 0 && Number(this.mArrGrid[piRowId]['NoOfChqs']) > 0) {
      this.mArrGrid[piRowId]['EndChqNo'] = Number(this.mArrGrid[piRowId]['StartChqNo']) + Number(this.mArrGrid[piRowId]['NoOfChqs']) - 1;
    } else if (psField == "NoOfChqs") {
      if (Number(this.mArrGrid[piRowId]['StartChqNo']) > 0 && Number(this.mArrGrid[piRowId]['NoOfChqs']) > 0) {
        this.mArrGrid[piRowId]['EndChqNo'] = Number(this.mArrGrid[piRowId]['StartChqNo']) + Number(this.mArrGrid[piRowId]['NoOfChqs']) - 1;
      }
    }
  }

  public bmlfOnValidateFromCheckBookData(): boolean {
    let pbFlag: boolean = true;
    let piRowNumber: number = 0;
    this.mArrGrid.forEach(ele => {
      piRowNumber += 1;
      if (ele.ChqBookID == "") {
        this.toastMessageService.notifyError(this.appUtility.bmgfonTranslate('CheckBookMaster.Msg.Key3') + " " + piRowNumber);
        pbFlag = false;
      } else if (ele.NoOfChqs == undefined || ele.NoOfChqs == "") {
        this.toastMessageService.notifyError(this.appUtility.bmgfonTranslate('CheckBookMaster.Msg.Key2') + " " + piRowNumber);
        pbFlag = false;
      } else if (ele.NoOfChqs != (ele.EndChqNo - ele.StartChqNo + 1)) {
        this.toastMessageService.notifyError(this.appUtility.bmgfonTranslate('CheckBookMaster.Msg.Key1') + " " + piRowNumber);
        pbFlag = false;
      }
    });
    return pbFlag;
  }



       /** -- dubicate entry valication for Box1099 --- */
       bmlfvalicationDubicatecheck(pobjEvent,rowIndex,type) {
        let pbResult: boolean = true;
        this.mArrGrid.forEach((ele,i) => {
          if(pbResult == true) {
              if(type == 'ChqBookID' && pobjEvent !=''){
                if(ele.ChqBookID.toUpperCase() == pobjEvent.toUpperCase() && i != (rowIndex)) {
                    this.toastMessageService.notifyError( 'Sq. No. ' + " - " + (rowIndex + 1)+' '+ 'has duplicate check book ID');
                    this.mArrGrid[rowIndex].ChqBookID ='';
                    pbResult = false;
                  }
                }
             }
        });
        return pbResult;
      }
  



  public bmlfActive_ChangeEvent(event: any): void {
    if (event.currentTarget.id == "checkShowInactiveCheckBook") {
      if (event.currentTarget.checked) {
        // this.mbShowOnlyActiveCheck = true;
        this.mArrGrid = [];
        this.mArrGrid = this.mArrGrid_AllData;
      } else {
        // this.mbShowOnlyActiveCheck = false;
        let mArrGridTemp = [];
        mArrGridTemp = this.mArrGrid_AllData;
        this.mArrGrid = [];
        mArrGridTemp.forEach(ele => {
          if (ele.ChqBookActiveInActive.toString() == "true") {
            this.mArrGrid.push(ele);
          }
        });
      }
    }
  }

