  public bmlfGetBankBalance(BankCashBalanceModel): void {

    let psBalance: String;
    let psPayment: String;
    let psNetBalance: String;
    let psBalanceHomeCurr: String;
    let psPaymentHomeCurr: String;
    let psNetBalanceHomeCurr: String;

    this.mObjBankCashBalanceModel = BankCashBalanceModel;
    if (this.mObjBankCashBalanceModel.BalanceBankCurrency >= 0) {
      psBalance = this.mObjBankCashBalanceModel.BalanceBankCurrency.toFixed(2).toString() + "Dr.";
    } else {
      this.mObjBankCashBalanceModel.BalanceBankCurrency = - this.mObjBankCashBalanceModel.BalanceBankCurrency;
      psBalance = this.mObjBankCashBalanceModel.BalanceBankCurrency.toFixed(2).toString() + "Cr.";
    }

    if (this.mObjBankCashBalanceModel.BalanceHomeCurrency >= 0) {
      psBalanceHomeCurr = this.mObjBankCashBalanceModel.BalanceHomeCurrency.toFixed(2).toString() + "Dr.";
    } else {
      this.mObjBankCashBalanceModel.BalanceHomeCurrency = - this.mObjBankCashBalanceModel.BalanceHomeCurrency;
      psBalanceHomeCurr = this.mObjBankCashBalanceModel.BalanceHomeCurrency.toFixed(2).toString() + "Cr.";
    }

    psPayment = this.mObjBankCashBalanceModel.PaymentsBankCurrency.toFixed(2).toString();
    psPaymentHomeCurr = this.mObjBankCashBalanceModel.PaymentsHomeCurrency.toFixed(2).toString();

    if (this.mObjBankCashBalanceModel.NetBalanceBankCurrency >= 0) {
      psNetBalance = this.mObjBankCashBalanceModel.NetBalanceBankCurrency.toFixed(2).toString() + "Dr.";
    } else {
      this.mObjBankCashBalanceModel.NetBalanceBankCurrency = - this.mObjBankCashBalanceModel.NetBalanceBankCurrency;
      psNetBalance = this.mObjBankCashBalanceModel.NetBalanceBankCurrency.toFixed(2).toString() + "Cr.";
    }

    if (this.mObjBankCashBalanceModel.NetBalanceHomeCurrency >= 0) {
      psNetBalanceHomeCurr = this.mObjBankCashBalanceModel.NetBalanceHomeCurrency.toFixed(2).toString() + "Dr.";
    } else {
      this.mObjBankCashBalanceModel.NetBalanceHomeCurrency = - this.mObjBankCashBalanceModel.NetBalanceHomeCurrency;
      psNetBalanceHomeCurr = this.mObjBankCashBalanceModel.NetBalanceHomeCurrency.toFixed(2).toString() + "Cr.";
    }

    this.msCurrentBalance = this.mObjModel.CurrencyKey + " " + psBalance + " | " + this.msHomeCurrency + " " + psBalanceHomeCurr;
    this.msUnpostedPayment = this.mObjModel.CurrencyKey + " " + psPayment + " | " + this.msHomeCurrency + " " + psPaymentHomeCurr;
    this.msNetBalance = this.mObjModel.CurrencyKey + " " + psNetBalance + " | " + this.msHomeCurrency + " " + psNetBalanceHomeCurr;
    // this.msODLimit = this.mObjModel.ODLimit.toFixed(2) + " " + this.msHomeCurrency;
  }

