  bmlfonValidateFormData() {

    if (this.mObjModel.BMEAcct.trim() == "") {
      if (this.mbSearchEvent == true) {
        return false;
      }
      this.toastMessageService.notifyError((this.appUtility.bmgfonTranslate('CashMaster.Msg.Key1')));
      return false;
    } else if (this.isEmpty(this.mObjModel.CurrencyKey)) {
      if (this.mbSearchEvent == true) {
        return false;
      }
      this.toastMessageService.notifyError((this.appUtility.bmgfonTranslate('CashMaster.Msg.Key2')));
      return false;
    } else if (this.isEmpty(this.mObjModel.CurrencyKey)) {
      if (this.mbSearchEvent == true) {
        return false;
      }
      this.toastMessageService.notifyError((this.appUtility.bmgfonTranslate('CashMaster.Msg.Key3')));
      return false;
    } else if (this.isEmpty(this.mObjModel.UserID)) {
      if (this.mbSearchEvent == true) {
        return false;
      }
      this.toastMessageService.notifyError((this.appUtility.bmgfonTranslate('CashMaster.Msg.Key5')));
      return false;
    } else if (this.isEmpty(this.mObjModel.Name)) {
      if (this.mbSearchEvent == true) {
        return false;
      }
      this.toastMessageService.notifyError((this.appUtility.bmgfonTranslate('CashMaster.Msg.Key6')));
      return false;
    }

    //Validate Field Extender -- Start
    let resultString = this.FieldExtender.bmgfValidationCheck();
    if (resultString.value == false) {
      if (this.mbSearchEvent == true) {
        return false;
      }
      this.toastMessageService.notifyWarning(resultString.msg);
      return false;
    }
    return true;
  }

