  bmlfonDeleteDialogAction(action) {

    if (action == 'No') {
      this.mbShowDeleteDialog = false;
    } else if (action == 'Yes') {
      if (this.msFormMode == "I" && (this.mbChange == true || this.FieldExtender.mbChange == true || this.customCtrl.mbChange == true)) {
        this.mbShowDeleteDialog = false;
        this.bmlfonClearControl();
        return;
      } else if (this.mbInsert && this.mbChange) {
        this.mbShowDeleteDialog = false;
        let deleteKey = this.appUtility.bmgfonTranslate('CashMaster.Msg.Delete') + '-' + this.mObjModel.CashID;
        this.FieldExtender.bmgfDeleteData(deleteKey);
        this.bmlfonClearControl();
        this.toastMessageService.notifyWarning(this.appUtility.bmgfonTranslate('COMMON.MSG.DELETE'));
        return false;
      }
      let reqData: any = {
        JSON: JSON.stringify({
          HeaderData: [{
            CashID: this.mObjModel.CashID
          }],
          Response: [{
            "ResponseText": "", "ErrorCode": ""
          }]
        })
      };
      this.objService.bmlfonDeleteData(reqData).subscribe(res => {
        if (res == null) {
          this.toastMessageService.notifyError(this.appUtility.bmgfonTranslate('COMMON.MSG.SERVERERROR'));
          return false;
        }
        if (res.Response[0].ErrorCode == "") {
          {
            if (res.Response[0].ResponseText.search("Reference") != -1) {
              this.toastMessageService.notifyError(this.appUtility.bmgfonTranslate('COMMON.MSG.EXIST_REF'));
              this.mbShowDeleteDialog = false;
              return;
            } else if (res.Response[0].ResponseText.toUpperCase().search('DELETED') != -1) {
              this.mbShowDeleteDialog = false;
              this.toastMessageService.notifySuccess((this.appUtility.bmgfonTranslate('COMMON.MSG.DELETE')));
              this.bmlfonClearControl();
            } else {
              this.toastMessageService.notifyError(res.Response[0].ResponseText);
              this.mbShowDeleteDialog = false;
            };
          }
        } else {
          this.toastMessageService.notifyError(res.Response[0].ResponseText);
          this.mbShowDeleteDialog = false;
        }
      });
    }
  }

