  bmlfopenCustomLookupModal(psScreenName, psLookupCaption, psLookUpKey, psEvent, psValue, psType, psEleId, MutiSelectionLookup = false) {
    if (psEvent == 'blur' && this.msLookupField == psValue) {
      this.msLookupField = '';
      return false;
    }
    this.marrLookupGridData = [];
    this.mobjLookupKeyData = {};
    this.mbMutiSelectionLookup = MutiSelectionLookup;
    this.mbIsLoading = true;
    let psDBName = JSON.parse(localStorage.getItem('usersession')).CmpDbName;
    let pobjReqData = {
      ScreenName: 'CashMasterComponent',
      LookUpKey: 'UserID',
      Filter1: psDBName
    }
    this.userService.GETGeneralSqlData(pobjReqData).subscribe(res => {
      this.mbIsLoading = false;
      if (res == null) {
        return false;
      }
      if (Object.entries(res).length > 0 && res.LookupData.length > 0) {
        this.marrLookupGridData = res.LookupData;
        //this.mslineItemRowIndex = rowIdx;
        this.mobjLookupKeyData.field = psLookupCaption;
        this.mobjLookupKeyData.gridData = this.marrLookupGridData;
        this.mobjLookupKeyData.type = psType;
        this.mobjLookupKeyData.eleId = psEleId;
        let parrValue = [];
        // parrValue =psValue.
        if (psValue) {
          this.mobjLookupKeyData.searchValue = '';//psValue;
        } else {
          this.mobjLookupKeyData.searchValue = '';
        }
        if (psEvent == 'blur' && psValue) {
          let inputKey = psLookupCaption;
          let filterdArr: any = [];
          let firstInput = Object.keys(this.marrLookupGridData[0])[0];
          filterdArr = this.marrLookupGridData.filter(obj => obj[firstInput].toUpperCase().trim() == psValue.toUpperCase());
          if (filterdArr.length == 1) {
            this.mobjLookupKeyData.value = filterdArr[0]
            this.bmlfSaveLookupField(this.mobjLookupKeyData);
            this.mbIsLookupModal = false;
          } else {
            this.mbIsLookupModal = true;
          }
        }
        else if (psEvent == 'click') {
          this.mbIsLookupModal = true;
        }
      }
      else {
        this.toastMessageService.notifyError(this.appUtility.bmgfonTranslate('COMMON.MSG.LOOKUPNODATA'));
      }
    }, err => {
      console.log(err);
    })
  }
