  bmlfPrint(){    
      let dtFromDate:any;
      let pDefaultReportType = localStorage.getItem('reportType');
      let paarraySelected: Array<any> = [];
        paarraySelected.push({
          ReportName :this.msScreenName,
          AsOnDate:moment(this.mobjData.AsOnDate).format(this.msServeDateFormat),
          FromSegment1:this.mobjData.FromSegment1=='' ?'': this.mobjData.FromSegment1,
          ToSegment1:this.mobjData.ToSegment1=='' ?'': this.mobjData.ToSegment1,
          FromSegment2:  this.mobjData.FromSegment2=='' ?'': this.mobjData.FromSegment2,
          ToSegment2:this.mobjData.ToSegment2=='' ?'': this.mobjData.ToSegment2,
          FromSegment3:this.mobjData.FromSegment3=='' ?'': this.mobjData.FromSegment3,
          ToSegment3:this.mobjData.ToSegment3=='' ?'': this.mobjData.ToSegment3,
          FromSegment4:this.mobjData.FromSegment4=='' ?'': this.mobjData.FromSegment4,
          ToSegment4:this.mobjData.ToSegment4=='' ?'': this.mobjData.ToSegment4,
          FromAccountKey:this.mobjData.FromSegment5=='' ?'': this.mobjData.FromSegment5,
          ToAccountKey:this.mobjData.ToSegment5=='' ?'': this.mobjData.ToSegment5,
          // Segment1Name:this.msCaptionSegment1,
          // Segment2Name:this.msCaptionSegment2,
          // Segment3Name:this.msCaptionSegment3,
          // Segment4Name:this.msCaptionSegment4,
          // Segment5Name:this.msCaptionSegment5,
          ListingFlag:false,
          ListingVariable:'',
        });
      
      let  pobjReqData = {
            JSON: JSON.stringify({
              DistReport: paarraySelected,
              Response: [{ResponseText: '',ErrorCode: '',Pdfpath:''}]
            })
          };
        this.mbIsLoading=true;
        this.userFieldService.bmlfDISTReportProcess(pobjReqData).subscribe(res => {
        this.mbIsLoading=false;
         if (res == null) {
           this.toastMessageService.notifyError("Failure.");
         }
         
         if (res.Response[0].ErrorCode == "") {
           {
             let pPDFSrc = res.Response[0].Pdfpath;
             if (pDefaultReportType == 'Print') {
               this.utilService.bmgfPrintPDF(pPDFSrc);
             } else if (pDefaultReportType == 'NewTab') {
               this.utilService.bmgfOpenPDFNewTab(pPDFSrc);
             } else {
              this.utilService.bmgfDownloadFile(pPDFSrc, this.msScreenName,pDefaultReportType);
             }
  
           }
         } else {
           this.toastMessageService.notifyError(res.Response[0].ResponseText);
         }
       });
  
  }

