import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { DateFormatService } from 'src/app/core/services/dateFormat.service';
import { ToastMessageService } from 'src/app/core/services/toast-message.service';
import { AppUtility } from 'src/app/core/services/utility.services';
import { UserFieldService } from 'src/app/shared/http/userfield.service';
import { CommonConstants } from 'src/app/core/constants/common.const';
import { ModalService } from 'src/app/core/services/modal.service';
import { LookupService } from 'src/app/shared/http/lookup.service';
import * as moment from 'moment';
import { LocalStorageService } from 'src/app/core/services/local-storage.service';
import { BaseGrid } from 'src/app/shared/components/basegrid';
import { ContraService } from '../../../http/contra.service';
import { InputModel,RptParameterMap } from '../../../models/contra.model';
import { CustomValidator } from 'src/app/core/services/custom.validators';
import { parse } from 'path';
@Component({
  selector: 'app-contra',
  templateUrl: './contra.component.html'
})
export class ContraComponent extends BaseGrid implements OnInit {
  public gridDataList: any =[];
   getDataList(): void {
     throw new Error("Method not implemented.");
   }
  public mbLoadingEnabled = false;
  public mbIsLoading: boolean = false;
  public mobjUserData: any = {};
  public msScreenName: string = 'Contra';
  public msParmScreenName: string = 'Contra - Selection Criteria                                                                         ';
  public msCustomDateFormat: string = "";
  public msServeDateFormat: string = CommonConstants.gsServerDateFormat;
  public miQtyPrecision: number = 0;
  public miQtyPrecisionFormat: string = 'n' + this.miQtyPrecision;
  public miAmountPrecision: number = 0;
  public miAmountPrecisionFormat: string = 'n' + this.miAmountPrecision;
  public autoCorrect: boolean = true;
  /**---Object Initialize---- */
  public marrGridData: Array<any> = [];
  public mobjData: InputModel = new InputModel;
  public mobjTotal: any = {};
  /**---Lookup realted field--- */
  public mbIsLookupModal: boolean = false;
  public mobjLookupKeyData: any = {};
  public marrLookupGridData: any = [];
  public msModaltitle: string = ''
  public mnumLineItemRowIndex: number = -1;
  public msLookupField = '';
  /**---END : Lookup realted field--- */
  /**-------START: TASK Field ----*/
  public mbIsTaskModal: boolean = false;
  public mobjTaskData: any = {};
  /**-------END: TASK Field ----*/
  
  /**--------------START : SEGMENT--- */
  // public msCaptionSegment1: string = '';
  // public msCaptionSegment2: string = '';
  // public msCaptionSegment3: string = '';
  // public msCaptionSegment4: string = '';
  // public msCaptionSegment5: string = '';
  // public marrSegmentList: any = [];
  /**--------------START : SEGMENT--- */

  public msindexOfFromSegment : any ;
  public msindexOfToSegment : any;
  public msMsgData: any = {};
  public msgModal: boolean = false;

  /**-----Report parameter---- */
  public paramValueArray = [];
  public parameterValue: string = '';
  public msscreenModeUpdate = "I";

  constructor(private route: ActivatedRoute, private router: Router,
    private modalservice: ModalService,
    private lookupService: LookupService,
    public toastMessageService: ToastMessageService,
    public dateFormatService: DateFormatService,
    public utilService: AppUtility,
    public userFieldService: UserFieldService, public lsService: LocalStorageService,
    private formService: ContraService
  ) {  super(lsService, dateFormatService, userFieldService, toastMessageService, utilService);}


  ngOnInit() {
    
    /*--------- get screen permission --------*/
    this.utilService.getBMEScreePermission(this.msScreenName);
    /**--- Get default formats from localStorage--- */
    let parrDefaultFormat = JSON.parse(localStorage.getItem("defaultFormats"));
    parrDefaultFormat.forEach(ele => {
      if (ele.Default_Key == "CURR_DECIMAL") {
        this.miAmountPrecision = Number(ele.Default_Value);
        this.miAmountPrecisionFormat = 'n' + this.miAmountPrecision;
      }
    });
    let parrRoundingDefaults = JSON.parse(localStorage.getItem("RoundingDefaults"));
    this.miQtyPrecision = Number(parrRoundingDefaults[0].Default_Value);
    this.miQtyPrecisionFormat = 'n' + this.miQtyPrecision;
    /**--- Get default date format from service--- */
    this.msCustomDateFormat = this.dateFormatService.getDateFormat();
    this.mobjUserData = this.lsService.getUserSession();
    //let pdateCurrDate = new Date();
    this.getParam(this.msParmScreenName);
    this.exportData.translateFrom = '';
    this.setExportData({ 'pageName': this.msScreenName });
    
  }
 
  bmlfGetData() {
    let dtFromDate:any;
    let dtToDate:any;
    let pbResult = this.bmlfonValidateFormData();
    if (pbResult == false) {
      return;
    }
    this.mbIsLoading = true;
    this.marrGridData = [];
    let pobjReqData = {
      ScreenName: this.msScreenName,
      LookUpKey: 'Default',       
      Filter1: this.mobjData.Posted==true ?1:0,
      Filter2: this.mobjData.UnPosted==true ?1:0,
      Filter3: this.mobjData.ContraNoFrom=='' ?'': this.mobjData.ContraNoFrom,
      Filter4: this.mobjData.ContraNoTo=='' ?'zzzzzzzzzzzzzzzzzzzzzzzzzzzzzzzzzzzzzzzzzzzzz': this.mobjData.ContraNoTo,
    }
    this.userFieldService.GETGeneralSqlData(pobjReqData).subscribe(res => {
      this.mbIsLoading = false
      if (res.LookupData.length > 0) {
        
        sessionStorage.setItem('Contra', JSON.stringify(pobjReqData));
        this.router.navigate(['/main/bankcash/contra-dashboard']); 
      }else{
        this.toastMessageService.notifyError(this.utilService.bmgfonTranslate('CONTRA.NODATAFOUND'));
      }
    });
  }
  bmlfOpenErrorBlock(psMsg): void {
    this.toastMessageService.notifyError(this.utilService.bmgfonTranslate(psMsg));
  }
  /**--------Start: Lookup Functionality----- */
  bmlfGetRequestJSON(psField) {

    let psReqData = {};
    let piPosted:any;
    let piUnPosted:any;
    if (this.mobjData.Posted==true){
      piPosted=1;
    }else{
      piPosted=0;
    }
    if (this.mobjData.UnPosted==true){
      piUnPosted=1;
    }else{
      piUnPosted=0;
    }
    if (psField == "ContraNoFrom" ) {
      psReqData = {
        ScreenName: this.msScreenName, LookUpKey: "ContraNoFrom", Filter1: piPosted, Filter2: piUnPosted, Filter3: "", Filter4: '', Filter5: ''
      };
    } else {
      psReqData = {
        ScreenName: this.msScreenName, LookUpKey: "ContraNoTo", Filter1: piPosted, Filter2: piUnPosted, Filter3: "", Filter4: '', Filter5: ''
      };
    } ;
    return psReqData;

  }
  // Global focus Fn
  bmlfOnGlobalFocus(field, value) {
    if (typeof value === 'object' && value.constructor === Object) {
      let valueCopy = Object.assign({}, value);
      this.msLookupField = valueCopy[field];
    } else {
      this.msLookupField = value;
    }
  }
  // LOOKUP MODAL 
  bmlfOpenLookupModal(psField, psEvent, psValue, psType, psEleId, psTitle = "", pbFromBatchSeries = false) {
    
    if (!this.utilService.mbWrite) {
      this.toastMessageService.notifyWarning(this.utilService.bmgfonTranslate('COMMON.PERMISSION_WRITE'));
      return;
    };
    if (psEvent=='blur'){
      if ( psField=='ContraNoFrom' && psValue=='' ){
        this.mobjData.ContraNoFromDescription='';
      }  
      if ( psField=='ContraNoTo' &&  psValue=='' ){
        this.mobjData.ContraNoToDescription='';
      }  
    }
    
    this.mbIsLoading = true;
    this.marrLookupGridData = [];
    this.mobjLookupKeyData = {};
    this.mobjLookupKeyData.title = this.utilService.bmgfonTranslate(psTitle);
    if (psEvent == 'blur' && (psValue == undefined || psValue == '' || this.msLookupField == psValue)) {
      this.msLookupField = '';
      this.mbIsLoading = false;
      return false
    }
    let psReqData = this.bmlfGetRequestJSON(psField);

    this.lookupService.getLookup(psReqData).subscribe(res => {
      this.mbIsLoading = false;
      if (res == null) {
        this.bmlfOpenErrorBlock('COMMON.MSG.NODATA');
        return false;
      }
      if (Object.entries(res).length > 0 && res.LookupData.length > 0) {
        res.LookupData.forEach((ele) => {
          Object.keys(ele).forEach((key) => {
            let psKey = key.toUpperCase();
            if (psKey.search('DATE') != -1) {
              if (ele[key] && ele[key] != '') {
                ele[key] = moment(ele[key]).format(this.msCustomDateFormat.toUpperCase());
              } else {
                ele[key] = moment().format(this.msCustomDateFormat.toUpperCase());
              }
            }
          });
        })
        this.marrLookupGridData = res.LookupData;
        this.mnumLineItemRowIndex = -1;
        this.mobjLookupKeyData.field = psField;
        this.mobjLookupKeyData.gridData = this.marrLookupGridData;
        this.mobjLookupKeyData.type = psType;
        this.mobjLookupKeyData.eleId = psEleId;

        if (psValue) {
          this.mobjLookupKeyData.searchValue = psValue.toString();
        } else {
          this.mobjLookupKeyData.searchValue = '';
        }
        if (psEvent == 'blur' && psValue) {
          let parrFilterdArr: any = [];
          /** --- Check : enter key matching record found. if find single row then direct call save lookup function --*/
          let pobjFirstInput = Object.keys(this.marrLookupGridData[0])[0];
          parrFilterdArr = this.marrLookupGridData.filter(obj => {
            if (obj[pobjFirstInput].toString().toUpperCase() == psValue.toString().toUpperCase()) {
              return obj;
            }
          });
          if (parrFilterdArr.length == 1) {
            this.mobjLookupKeyData.value = parrFilterdArr[0];
            this.mobjLookupKeyData.valueKey = parrFilterdArr[0][pobjFirstInput];
            this.bmlfSaveLookupField(this.mobjLookupKeyData);
            this.mbIsLookupModal = false;
          } else {
            this.mbIsLookupModal = true;
          }
          // } 
        } else if (psEvent == 'click') {
          if (pbFromBatchSeries) {
            this.mbIsLookupModal = false;
            let pobj = Object.keys(this.marrLookupGridData[0])[0];
            this.mobjLookupKeyData.value = this.marrLookupGridData[0];
            this.mobjLookupKeyData.valueKey = this.marrLookupGridData[0][pobj];
            this.bmlfSaveLookupField(this.mobjLookupKeyData);
            return;
          }
          this.mbIsLookupModal = true;
        }
      } else {
        this.bmlfOpenErrorBlock('COMMON.MSG.NODATA');
      }
    }, err => {
      console.log(err);
      this.mbIsLoading = false;
    })
  }

  /** save lookup field data */
  bmlfSaveLookupField(pobjEvent) {
    this.mbIsLoading = false;
    this.mbIsLookupModal = false;
    if (pobjEvent.type == 'custom') {
      this.mobjData[pobjEvent.field] = pobjEvent.valueKey;
      this.mobjData[pobjEvent.field +'Description'] = pobjEvent.value.Description;
    }
  }
  bmlfGetInputColumn(psfield) {
    if (psfield == 'ContraNoFrom' || psfield == 'ContraNoTo') {
      return 'JrnlBtchNo'
    }
  }
  bmlfCloseLookupModal(pobjEvent): void {
    this.mbIsLookupModal = pobjEvent.isLookupModal;
    if (pobjEvent.eleId != null) {
      let psInputKey = this.bmlfGetInputColumn(pobjEvent.field);
      let parrFilterData: any = [];
      if (psInputKey) {
        if(pobjEvent.searchValue!=null){
          parrFilterData = pobjEvent.gridData.filter(obj => obj[psInputKey].toString().toUpperCase() == pobjEvent.searchValue.toUpperCase());
        }
      }
      if (parrFilterData.length == 0) {
        if (document.getElementById(pobjEvent.eleId) != undefined) {
          document.getElementById(pobjEvent.eleId)['value'] = "";
        }

        if (this.mobjData[pobjEvent.field] != undefined) {
          this.mobjData[pobjEvent.field] = '';
          this.mobjData[pobjEvent.field + "Description"] = '';
          setTimeout(function () { document.getElementById(pobjEvent.eleId).focus(); }, 10);
        }
      }
    }
    let elementExists = document.getElementById(pobjEvent.eleId);
    if (elementExists) {
      document.getElementById(pobjEvent.eleId).focus();
    }
  }

  bmlfClose(): void {
    this.router.navigate(["/main/dashboard/dashboardpage"]);
  }
  
  //Open Task MODAL 
  bmlfOpenTaskModal(taskType) {
    this.mobjTaskData = {};
    this.mobjTaskData.taskType = taskType;
    if (taskType == 'myTask') {
      this.mobjTaskData.title = this.utilService.bmgfonTranslate('MYTASK');
      this.mobjTaskData.innerRecord = this.mobjUserData;
    } else if (taskType == 'recordTask') {
      this.mobjTaskData.title = this.utilService.bmgfonTranslate('TASKFORRECORD');
      let pobjFormRec: any = {};
      pobjFormRec.formName = this.msScreenName;
      //pobjFormRec.primeKey = this.mobjData.AccountGroup;
      this.mobjTaskData.innerRecord = pobjFormRec;
    }
    this.mbIsTaskModal = true;
  }

  //Close Task MODAL 
  bmlfonCloseTaskModal(pobjData) {
    this.mbIsTaskModal = pobjData.isTaskModal;
  }

   getParam(scr) {
    
    this.mobjData.Posted=true;
    this.mobjData.UnPosted=true;
    
    this.mbIsLoading = true;
    this.formService.getReportParameter(scr).subscribe(res => {
      if (res != null) {
        this.paramValueArray = res.RPTPARAMETERS;
        this.mbIsLoading = false;
      } else {
        this.mbIsLoading = false;
      }
    })
  }
  bmlfonParamchange(e) {
    try {
      let parametername = e.target.value;
      if(parametername.trim() != ''){
        if(this.paramValueArray.length>0){
          let list = this.paramValueArray.filter(x => x.PARANAME === parametername)[0];
          this.bmlfFetchParamData(list.PARANAME)
        }
      }
    }
    catch (e) {
      console.log(e);
      console.trace();
    }
  }
  bmlfFetchParamData(param) {
    let pValue = param;
    this.mbIsLoading = true;
    this.formService.getReportParameterValue(this.msParmScreenName, param).subscribe(res => {
      if (res != null && res.RPTPARAMETERSBYVALUE.length > 0) {
        this.bmlfclearModel();
        this.fillControlFromParam(res.RPTPARAMETERSBYVALUE);
      }
      else {
        console.error('No Response');
        console.trace();
        this.mbIsLoading = false;
      }
    })
  }
  bmlfclearModel() {
    this.mobjData = new InputModel();
  }

  private fillControlFromParam(resData) {
    resData.forEach(element => {
      let key = this.bmlfongetKeyFromValue(element.CONTROLNAME);
      this.mobjData[key] = element.CONTROLVALUE;
    });
    
    if (this.mobjData.Posted.toString() =="1"){
      this.mobjData.Posted=true;
    }else{
      this.mobjData.Posted=false;
    }
    if (this.mobjData.UnPosted.toString() =="1"){
      this.mobjData.UnPosted=true;
    }else{
      this.mobjData.UnPosted=false;
    }
    this.bmlfGetDescription(this.mobjData.ContraNoFrom,this.mobjData.ContraNoTo);
    this.mbIsLoading = false;
  }
  bmlfongetKeyFromValue(value) {
    let newObj = this.mobjData;
    let tempKey = Object.keys(newObj).find(data => {
      return RptParameterMap[data] == value
    });
    return tempKey;
  }
  bmlfonParameterSave() {
    let paramValue = this.parameterValue; //this.paramCtrl.nativeElement.value;
    let newObj = this.mobjData;
    if (newObj.Posted==true){
      newObj.Posted='1';
    }else{
      newObj.Posted='0';
    }
    if (newObj.UnPosted==true){
      newObj.UnPosted='1';
    }else{
      newObj.UnPosted='0';
    }

    if (paramValue == '') {
      this.bmlfOpenErrorBlock('PRMEMPTY');
      return;
    } else {
      this.mbIsLoading = true;
      this.msscreenModeUpdate = "I";
      let jObj = {};
      let headerData = [];
      let jArray = [];
      let counterRecId = 0;
      jObj["ParamName"] = paramValue;
      jObj["ScreenName"] = this.msParmScreenName;
      if (this.paramValueArray.length>0){
        let list = this.paramValueArray.filter(x => x.PARANAME === paramValue)[0];
        if (list !=undefined){
          this.msscreenModeUpdate = 'U';
          jObj["UpdateMode"] = this.msscreenModeUpdate;
        } else{
          jObj["UpdateMode"] = this.msscreenModeUpdate;
        }
      } else {
        jObj["UpdateMode"] = this.msscreenModeUpdate;
      }

      headerData.push(jObj);
      Object.keys(newObj).map(key => {
        if (key != undefined && typeof (newObj[key]) == "string") {
          let tempJObj = {};
          if (RptParameterMap[key] != undefined) {
            tempJObj["ControlName"] = RptParameterMap[key];
            tempJObj["ControlValue"] = newObj[key];
            tempJObj["RecId"] = counterRecId++;
            jArray.push(tempJObj);
          }
        }
      });
      this.formService.saveReportParameters(jArray, headerData).subscribe(res => {
        if (res != null) {
          if (res['Response'][0].ResponseText == "Sucess") {
            this.parameterValue = '';
            this.toastMessageService.notifySuccess(this.utilService.bmgfonTranslate('SAVEMSG'));
            this.mbIsLoading = false;
            this.getParam(this.msParmScreenName);
          }
          else {
            console.trace();
            console.error('Save error');
            this.mbIsLoading = false;
          }
        }
      })
    }

  }

  bmlfonParameterDelete() {
    let oParaValue = this.parameterValue ;//this.paramCtrl.nativeElement.value;
    if (oParaValue == "") {
      this.bmlfOpenErrorBlock('SELRPTPRM');
      return;
    }
    else {
      //this.mbcheckval = false
      this.mbIsLoading = true;
      this.bmlfondeleteApiParam(oParaValue);
      this.parameterValue ='';
      return true;
    }
  }
  bmlfondeleteApiParam(oParaValue) {
    this.formService.DeleteReportParameter(this.msParmScreenName, oParaValue).subscribe(res => {
      if (res != null) {
        if (res.Response[0].ResponseText == "Success") {
          this.toastMessageService.notifySuccess(this.utilService.bmgfonTranslate('DELMSG'));
          this.parameterValue ='';
          this.bmlfclearModel();
          this.getParam(this.msParmScreenName);
          this.mbIsLoading = false;
          return true;
        }
        this.mbIsLoading = false;
      }

    })
  }

  bmlfViewHelpDoc() {
    window.open('../../../../assets/help files/' + this.msScreenName + '.pdf', '_blank');
  }

bmlfonValidateFormData() {
  if (this.mobjData.ContraNoFrom !='' && this.mobjData.ContraNoTo !=''){
    if (this.mobjData.ContraNoFrom > this.mobjData.ContraNoTo) {
      this.toastMessageService.notifyError((this.utilService.bmgfonTranslate('CONTRA.CONTRANOVALIDATE')));
      return false;
  }
  }
   return true;
}

bmlfGetDescription(ContraNoFrom,ContraNoTo) {
  
  // let piPosted, piUnPosted;
  // if (this.mobjData.Posted==true){
  //   piPosted=1;
  // }else{
  //   piPosted=0;
  // }
  // if (this.mobjData.UnPosted==true){
  //   piUnPosted=1;
  // }else{
  //   piUnPosted=0;
  // }

  //let pobjReqData = {ScreenName: this.msScreenName,LookUpKey: 'Description',Filter1: piPosted,Filter2: piUnPosted,Filter3: ContraNoFrom}
  let pobjReqData = {ScreenName: this.msScreenName,LookUpKey: 'Description',Filter1: 1,Filter2: 1,Filter3: ContraNoFrom}
  this.userFieldService.GETGeneralSqlData(pobjReqData).subscribe(res => {
    this.mbIsLoading = false
    if (res.LookupData.length > 0) {
      this.mobjData.ContraNoFromDescription=res.LookupData[0].Description;
    }
  });

  //let pobjReqData1 = {ScreenName: this.msScreenName,LookUpKey: 'Description',Filter1: piPosted,Filter2: piUnPosted,Filter3: ContraNoTo}
  let pobjReqData1 = {ScreenName: this.msScreenName,LookUpKey: 'Description',Filter1: 1,Filter2: 1,Filter3: ContraNoTo}
  this.userFieldService.GETGeneralSqlData(pobjReqData1).subscribe(res => {
    this.mbIsLoading = false
    if (res.LookupData.length > 0) {
      this.mobjData.ContraNoToDescription=res.LookupData[0].Description;
    }
  });

}



}
