  //---------------------------------------------------------------------------------------------
  bmlfResetGridSettings() {
    this.bmlfOnResetGridSettings("Banking GL Entry", "Banking GL Entry");
  }
  //---------------------------------------------------------------------------------------------    
  bmlfSaveGridSetting(): void {
    this.bmlfOnSaveGrid("Banking GL Entry");
  }
  //---------------------------------------------------------------------------------------------
  //Add
  bmlfonAddBankingGL(): void {
    //check Write permission
    if (!this.objUtil.mbWrite) {
      this.objToastService.notifyWarning(this.objUtil.bmgfonTranslate('COMMON.PERMISSION_WRITE'));
      return;
    };

    this.router.navigate(['/main/bankcash/banking-gl-entry/manage']);


  }
  //---------------------------------------------------------------------------------------------
  //Edit
  bmlfonEdit(data) {
    this.router.navigate(['/main/bankcash/banking-gl-entry/manage'], { queryParams: { JrnlBtchNo: data.JrnlBtchNo, Status: data.Status } });

  }
  //---------------------------------------------------------------------------------------------
  //copy
  bmlfonCopy(data) {
    //check Write permission
    if (!this.objUtil.mbWrite) {
      this.objToastService.notifyWarning(this.objUtil.bmgfonTranslate('COMMON.PERMISSION_WRITE'));
      return;
    };
    this.router.navigate(['/main/bankcash/banking-gl-entry/manage'], { queryParams: { JrnlBtchNo: data.JrnlBtchNo, Status: data.Status, CopyRecord: "Y" }, skipLocationChange: true });

  }
  //---------------------------------------------------------------------------------------------
  //Delete record
  bmlfonDelete(data) {
    //check delete permission
    if (!this.objUtil.mbDelete) {
      this.objToastService.notifyWarning(this.objUtil.bmgfonTranslate('COMMON.PERMISSION_DELETE'));
      return;
    };
    if (data.Status != 'NEW') {
      this.msMsgData.Data = this.objUtil.bmgfonTranslate('BANKINGGLENTRY.MSG.DELETECHECK');
      this.msMsgData.Header = "BatchMaster";
      this.msMsgData.mbBulkDeleteView = true;
      this.mbBulkDeleteView = true;
      return;
    } else {
      this.msJrnlBtchNo = data.JrnlBtchNo;
      this.mObjModel.JrnlBtchNo = this.msJrnlBtchNo;
      this.mbShowDeleteDialog = true;
    }
  }
  //---------------------------------------------------------------------------------------------
  //Bulk Delete
  onActionDelete() {
    //check delete permission
    if (!this.objUtil.mbDelete) {
      this.objToastService.notifyWarning(this.objUtil.bmgfonTranslate('COMMON.PERMISSION_DELETE'));
      return;
    };

    if (this.maarraySelected.length == 0) {
      this.objToastService.notifyError(this.objUtil.bmgfonTranslate('BANKINGGLENTRY.MSG.DELETEEMPTY'))
      return;
    }

    // this.maarraySelected = this.maarraySelected.map(data => {
    //   return data.dataItem;
    // })


    this.mbBulkDelete = true;
    this.mbShowDeleteDialog = true;
  }
