import { ContraEntryConstants } from '../../../../constants/contra-entry.constants';
import * as moment from 'moment';
import { CommonConstants } from 'src/app/core/constants/common.const';

@Component({
  selector: 'app-contra-entry-list',
  templateUrl: './contra-entry-list.component.html'
})
export class ContraEntryListComponent extends BaseGrid implements OnInit {
  public bmlfSelectedCallback = (args) => args.dataItem;
  public gridDataList = [];
  public orginalGridList = [];
  public mbloadingEnabled = false;
  public gridSettings: GridSettings = new GridSettings();
  public mscustomDateFormat: string = "";
  public mbShowDeleteDialog: boolean = false;
  public mObjModel: CntraEntryModel = new CntraEntryModel();
  public msJrnlBtchNo: string = "";
  public maarraySelected: Array<any> = [];
  //public tmpmaarraySelected: Array<any> = [];
  public mbBulkDelete: boolean = false;
  public msScreenName: string = 'Contra Entry';
  public msI18Name: string = 'CONTRAENTRY';
  public msLookupKey: string = 'ContraEntryUnPostedList';
  // public msLookupUnpostedKey: string = 'ContraEntryUnPostedList';
  // public msLookupPostedKey: string = 'ContraEntryPostedList';
  public msMsgData: any = {};
  public mbBulkDeleteView: boolean = false;
  public msRoutePath: string = '/main/bankcash/contra-entry';
  public msManageRoutePath: string = '/main/bankcash/contra-entry/manage';
  //public msPosted: string = "Posted";
  public msUnposted: string = "Unposted";
  public mbReportDialog: boolean = false;
  public mbUnPosted: boolean = false;
  public msReportDtl: string = "";
  // public msUnpostedcount: string = "Unposted";
  // public msPostedcount: string = "Posted";
  public marrFilterList = [];

  constructor(private router: Router,
    public renderer: Renderer2,
    private lsService: LocalStorageService,
    private ContraEntryService: ContraEntryService,
    public dateFormatService: DateFormatService, public objToastService: ToastMessageService,
    public objUtil: AppUtility,
    public userFieldService: UserFieldService) {
    super(lsService, dateFormatService, userFieldService, objToastService, objUtil);
  }
  //---------------------------------------------------------------------------------------------

  ngOnInit() {
    this.mscustomDateFormat = this.dateFormatService.getDateFormat();
    this.initGrid();
    this.objUtil.getBMEScreePermission(this.msScreenName);
    this.msUnposted = 'Unposted';
    this.mbUnPosted = true;
    this.getDataList();
    this.setExportData({ 'pageName': this.msScreenName });
  }
  //---------------------------------------------------------------------------------------------

  getDataList(psFilterType = ''): void {
    
    this.mbloadingEnabled = true;
    //let headerData: string = ""
    let EntryType: string = this.msUnposted;
    if (EntryType == 'Posted') {
      this.msLookupKey = 'ContraEntryPostedList';
    } else {
      this.msLookupKey = 'ContraEntryUnPostedList';
    }
    const headerData = { "ScreenName": this.msScreenName, "LookUpKey": this.msLookupKey, "Filter1": "", "Filter2": "", "Filter3": "", "Filter4": "", "Filter5": "" }

    this.ContraEntryService.bmlfonGetList(headerData).subscribe(

      res => {
        let parrGridData = res.LookupData;
        parrGridData.forEach(ele => {
          ele.RecDate = new Date(ele.RecDate);
          ele.Date = new Date(ele.Date);
        })

        this.gridDataList = parrGridData;
        this.orginalGridList = parrGridData;
        let parrSettingData = res.GridSettingData[0].JSONString;
        this.setInitGridSettings(parrSettingData, this.msI18Name);
        this.mobjGridSettings.state.take=50;
        if (psFilterType == '') {
        this.bmlfGetFiltersCounts();
        }
      }
      , error => {
        console.log(error);
      }

    ).add(() => { this.mbloadingEnabled = false })
  }

  bmlfGetFiltersCounts() {
    let pobjReqData = {
      ScreenName: this.msScreenName,
      LookUpKey: "FilterCount"
    };
    this.userFieldService.GETGeneralSqlData(pobjReqData).subscribe(res => {
      let resList = [];
      if (res.Table1 != undefined && res.Table1.length > 0) {
        let convToJson = res.Table1[0].RecCount;
        resList.push(JSON.parse(convToJson));
      }

      if (res.LookupData != undefined && res.LookupData.length > 0) {
        let convToJson = res.LookupData[0].RecCount;
        resList.push(JSON.parse(convToJson));
      }
      this.marrFilterList = [];
      resList.forEach(ele => {
        this.marrFilterList.push(ele);
      });
    }, err => {
      console.log(err);
    });
    // let parrReqData = [{
    //   ScreenName: "Contra Entry",
    //   LookUpKey: "ContraPostedCount"
    // }, {
    //   ScreenName: "Contra Entry",
    //   LookUpKey: "ContraUnpostedCount"
    // }]
    // let apiName = '/bme/GETGeneralSqlData';
    // this.userFieldService.bmlfRunMultipleAPI(parrReqData, apiName).subscribe(res => {
    //   let resList = [];
    //   res.forEach(ele => {
    //     if (ele.LookupData[0].RecCount) {
    //       let convToJson = ele.LookupData[0].RecCount;
    //       resList.push(JSON.parse(convToJson));
    //     }
    //   });

    //   resList.forEach(ele => {
    //  //   console.log('count', ele)
    //     if (ele.Field == "UnPost") {
    //       this.msUnpostedcount = "Unposted(" + String(ele.Count) + ")";
    //     }
    //     if (ele.Field == "Post") {
    //       this.msPostedcount = "Posted(" + String(ele.Count) + ")";
    //     }

    //   });
    // }, err => {
    //   console.log(err);
    // });
  }
  //---------------------------------------------------------------------------------------------
  bmlfResetGridSettings() {
    this.bmlfOnResetGridSettings(this.msScreenName, this.msI18Name);
  }
  //---------------------------------------------------------------------------------------------  

  bmlfSaveGridSetting(): void {
    this.bmlfOnSaveGrid(this.msScreenName);
  }
  //---------------------------------------------------------------------------------------------  
  //Add
  bmlfonAddContra(): void {
    //check Write permission
    if (!this.objUtil.mbWrite) {
      this.objToastService.notifyWarning(this.objUtil.bmgfonTranslate('COMMON.PERMISSION_WRITE'));
      return;
    };

    this.router.navigate([this.msManageRoutePath]);
  }
  //---------------------------------------------------------------------------------------------
  //Edit
  bmlfonEdit(data) {
    
    this.router.navigate([this.msManageRoutePath], { queryParams: { JrnlBtchNo: data.JrnlBtchNo, Status: data.Status } });
  }
  //---------------------------------------------------------------------------------------------
  //copy
  bmlfonCopy(data) {
    //check Write permission
    if (!this.objUtil.mbWrite) {
      this.objToastService.notifyWarning(this.objUtil.bmgfonTranslate('COMMON.PERMISSION_WRITE'));
      return;
    };
    this.router.navigate([this.msManageRoutePath], { queryParams: { JrnlBtchNo: data.JrnlBtchNo, Status: data.Status, CopyRecord: "Y" }, skipLocationChange: true });

  }
  //---------------------------------------------------------------------------------------------
  //Delete record
  bmlfonDelete(data) {
    
    //check delete permission
    if (!this.objUtil.mbDelete) {
      this.objToastService.notifyWarning(this.objUtil.bmgfonTranslate('COMMON.PERMISSION_DELETE'));
      return;
    };
    if (data.Status == 'NEW') {
      this.msJrnlBtchNo = data.JrnlBtchNo;
      this.mObjModel.JrnlBtchNo = this.msJrnlBtchNo;
      this.mbShowDeleteDialog = true;
    } else {
      this.objToastService.notifyError(this.objUtil.bmgfonTranslate('CONTRAENTRY.VALID.DELETECHECK'));
      return;
    }
  }
  //---------------------------------------------------------------------------------------------
  //Bulk Delete
  onActionDelete() {
    
    //check delete permission
    if (!this.objUtil.mbDelete) {
      this.objToastService.notifyWarning(this.objUtil.bmgfonTranslate('COMMON.PERMISSION_DELETE'));
      return;
    };

    if (this.maarraySelected.length == 0) {
      this.objToastService.notifyError(this.objUtil.bmgfonTranslate('CONTRAENTRY.VALID.DELETEEMPTY'))
      return;
    }

    let pArrNewStatusRecord = this.maarraySelected.filter(x => x['Status'] == 'NEW');
    if (pArrNewStatusRecord.length == 0) {
      this.objToastService.notifyError(this.objUtil.bmgfonTranslate('CONTRAENTRY.VALID.DELETECHECK'))
      return;
    }

    this.mbBulkDelete = true;
    this.mbShowDeleteDialog = true;
  }
  //---------------------------------------------------------------------------------------------
  bmlfonfilterEvent(obj) {
    if (obj.Type == "String") {
      let filterGrid = this.orginalGridList.filter(x => x[obj.FirstKey] == obj.FirstValue);
      this.gridDataList = filterGrid;
    }
    else if (obj.Type == "Date") {
      let filterGrid = this.orginalGridList.filter(x => (new Date(x[obj.FirstKey]).getTime() >= obj.FirstValue.getTime()) && new Date(x[obj.FirstKey]).getTime() <= obj.SecondValue.getTime());
      this.gridDataList = filterGrid;
    }
  }
  //---------------------------------------------------------------------------------------------
  bmlfonDialogAction(action) {
    
    if (action == 'No') {
      this.mbShowDeleteDialog = false;
    } else if (action == 'Yes') {

      if (this.mbBulkDelete == true) {
        let pArrNewStatusRecord = this.maarraySelected.filter(x => x['Status'] == 'NEW');
        let reqData: any = {
          JSON: JSON.stringify({
            HeaderData: pArrNewStatusRecord,
            Response: [{
              "ResponseText": "", "ErrorCode": ""
            }]
          })
        };
        this.ContraEntryService.bmlfonDeleteData(reqData).subscribe(res => {
          if (res == null) {
            this.objToastService.notifyError('Server Error');
            return false;
          }
          if (res.Response[0].ErrorCode == "") {
            {
              //this.objToastService.notifySuccess(res.Response[0].ResponseText);
              this.msMsgData.Data = res.Response[0].ResponseText;
              this.msMsgData.Header = "BatchMaster";
              this.msMsgData.mbBulkDeleteView = true;
              this.mbBulkDeleteView = true;

              this.mbShowDeleteDialog = false;
              this.mbBulkDelete = false;
              this.maarraySelected = [];
              this.getDataList();
            }
          } else {
            this.objToastService.notifyError(res.Response[0].ResponseText)
            this.mbShowDeleteDialog = false;
            this.getDataList();
          }

        })
      }
      else {
        let reqData: any = {
          JSON: JSON.stringify({
            HeaderData: [{ ... this.mObjModel }],
            Response: [{
              "ResponseText": "", "ErrorCode": ""
            }]
          })

        };
        this.ContraEntryService.bmlfonDeleteData(reqData).subscribe(res => {
          if (res == null) {
            this.objToastService.notifyError('Server Error');
            return false;
          }
          if (res.Response[0].ErrorCode == "") {
            {
              if (res.Response[0].ResponseText.toUpperCase().search('DELETED') != -1) {
                this.objToastService.notifySuccess((this.objUtil.bmgfonTranslate('COMMON.MSG.DELETE')));
                this.mbShowDeleteDialog = false;
                this.mbBulkDelete = false;
                this.maarraySelected = [];
                this.getDataList();
              } else {
                this.objToastService.notifyError(res.Response[0].ResponseText);
                this.maarraySelected = [];
                this.mbShowDeleteDialog = false;
              }
            }
          } else {
            this.objToastService.notifyError(res.Response[0].ResponseText)
          }
        })
      }

    }
  }
  //-----------------------------------------------------------------------------------------
  bmlfonselectionChange(e) {

    if (e.selectedRows.length > 0) {
      //this.maarraySelected = [...this.maarraySelected, ...e.selectedRows];
      e.selectedRows.forEach(ele => {
        this.maarraySelected.push(ele.dataItem);
      })

    }
    else if (e.deselectedRows.length > 0) {
      // let indexArray = e.deselectedRows.map(data => data.index);
      // this.maarraySelected = this.maarraySelected.filter(element => {
      //   return !indexArray.includes(element.index)
      // })
      e.deselectedRows.forEach(ele => {
        let pobjTempData = ele.dataItem;
        this.maarraySelected = this.maarraySelected.filter(x => {
          if (x.JrnlBtchNo != pobjTempData.JrnlBtchNo) {
            return x;
          }
        })
      })
  //    console.log(this.maarraySelected);
    }
  }
  //--------------------------------------------------------------------------------------------
  public bmlfoncloseModal(event) {
    this.mbBulkDeleteView = event.mbBulkDeleteView;
  }
  //--------------------------------------------------------------------------------------------
  pageChange(e) {
    //this.maarraySelected = [];
  }

  bmlfGetEntry(EntryType) {
    
    this.msUnposted = EntryType == "Post" ? "Posted" : "Unposted";
    this.maarraySelected=[];
    if (this.msUnposted == "Unposted") {
      this.mbUnPosted = true;
    } else if (this.msUnposted == "Posted") {
      this.mbUnPosted = false;
    }
    this.getDataList('filtered');
  }
  //--------------------------------------------------------------------------------------------

  bmlfActionPost() {
    //check Write permission
    if (!this.objUtil.mbWrite) {
      this.objToastService.notifyWarning(this.objUtil.bmgfonTranslate('COMMON.PERMISSION_WRITE'));
      return;
    };

    if (this.maarraySelected.length == 0) {
      return;
    }
    let pArrNewStatusRecord = this.maarraySelected.filter(x => x['Status'] == 'NEW');
    // if (data.PostingDate > new Date()) { //SCR:141127
    //   this.toastMessageService.notifyError((this.objUtil.bmgfonTranslate('JOURNALENTRY.VALID.VALIDPOSTINGDATE')));
    //   return false;
    // }
    let reqData: any = {
      JSON: JSON.stringify({
        HeaderData: pArrNewStatusRecord,
        ScreenName: [{
          CallType: this.msScreenName,
        }],
        Response: [{
          "ResponseText": "", "ErrorCode": ""
        }]
      })
    };
    this.ContraEntryService.bmlfonPostData(reqData).subscribe(res => {
      if (res == null) {
        this.toastMessageService.notifyError('Server Error');
        return false;
      }
      if (res.Response[0].ErrorCode == "") {
        {
          this.msReportDtl = this.objUtil.bmgfonTranslate(res.Response[0].ResponseText);
          this.bmlfReportModal('open');
          this.maarraySelected = [];
          this.getDataList();
        }
      } else {
        this.toastMessageService.notifyError(res.Response[0].ResponseText);
      }
    });
  }

  bmlfPost(data) {
    
    if (data.Status != 'NEW') {
      return;
    }
    if (data.PostingDate > new Date()) { //SCR:141127
      this.toastMessageService.notifyError((this.objUtil.bmgfonTranslate('CONTRAENTRY.VALID.VALIDPOSTINGDATE')));
      return false;
    }
    let reqData: any = {
      JSON: JSON.stringify({
        HeaderData: [{
          JrnlBtchNo: data.JrnlBtchNo,
        }],
        ScreenName: [{
          CallType: this.msScreenName,
        }],
        Response: [{
          "ResponseText": "", "ErrorCode": ""
        }]
      })
    };
    this.ContraEntryService.bmlfonPostData(reqData).subscribe(res => {
      if (res == null) {
        this.toastMessageService.notifyError('Server Error');
        return false;
      }
      if (res.Response[0].ErrorCode == "") {
        {
          this.msReportDtl = this.objUtil.bmgfonTranslate(res.Response[0].ResponseText);
          this.bmlfReportModal('open');
          this.maarraySelected = [];
          this.getDataList();
        }
      } else {
        this.toastMessageService.notifyError(res.Response[0].ResponseText);
        this.maarraySelected = [];
      }
    });
  }

  bmlfActionRelease() {
    //check Write permission
    if (!this.objUtil.mbWrite) {
      this.objToastService.notifyWarning(this.objUtil.bmgfonTranslate('COMMON.PERMISSION_WRITE'));
      return;
    };

    if (this.maarraySelected.length == 0) {
      return;
    }

    let pArrNewStatusRecord = this.maarraySelected.filter(x => x['Status'] == 'HOLD');
    let reqData: any = {
      JSON: JSON.stringify({
        HeaderData: pArrNewStatusRecord,
        ScreenName: [{
          CallType: this.msScreenName,
        }],
        Response: [{
          "ResponseText": "", "ErrorCode": ""
        }]
      })
    };
    this.ContraEntryService.bmlfonReleaseData(reqData).subscribe(res => {
      if (res == null) {
        this.toastMessageService.notifyError('Server Error');
        return false;
      }
      if (res.Response[0].ErrorCode == "") {
        {
          this.msReportDtl = this.objUtil.bmgfonTranslate(res.Response[0].ResponseText);
          this.bmlfReportModal('open');
          this.maarraySelected = [];
          this.getDataList();
        }
      } else {
        this.toastMessageService.notifyError(res.Response[0].ResponseText);
      }
    });
  }

  bmlfRelease(data) {
    if (data.Status == 'HOLD') {
      let reqData: any = {
        JSON: JSON.stringify({
          HeaderData: [{
            JrnlBtchNo: data.JrnlBtchNo,
          }],
          ScreenName: [{
            CallType: this.msScreenName,
          }],
          Response: [{
            "ResponseText": "", "ErrorCode": ""
          }]
        })
      };
      this.ContraEntryService.bmlfonReleaseData(reqData).subscribe(res => {
        if (res == null) {
          this.toastMessageService.notifyError('Server Error');
          return false;
        }
        if (res.Response[0].ErrorCode == "") {
          {
            // this.msReportDtl = this.objUtil.bmgfonTranslate(res.Response[0].ResponseText);
            // this.bmlfReportModal('open');
            this.toastMessageService.notifySuccess(res.Response[0].ResponseText);
            this.maarraySelected = [];
            this.getDataList();
          }
        } else {
          this.toastMessageService.notifyError(res.Response[0].ResponseText);
          this.maarraySelected = [];
        }
      });
    }
  }

  bmlfActionHold() {
    //check Write permission
    if (!this.objUtil.mbWrite) {
      this.objToastService.notifyWarning(this.objUtil.bmgfonTranslate('COMMON.PERMISSION_WRITE'));
      return;
    };

    if (this.maarraySelected.length == 0) {
      return;
    }
    
    let pArrNewStatusRecord = this.maarraySelected.filter(x => x['Status'] == 'NEW');
    let reqData: any = {
      JSON: JSON.stringify({
        HeaderData: pArrNewStatusRecord,
        ScreenName: [{
          CallType: this.msScreenName,
        }],
        Response: [{
          "ResponseText": "", "ErrorCode": ""
        }]
      })
    };
    this.ContraEntryService.bmlfonHoldData(reqData).subscribe(res => {
      if (res == null) {
        this.toastMessageService.notifyError('Server Error');
        return false;
      }
      if (res.Response[0].ErrorCode == "") {
        {
          this.msReportDtl = this.objUtil.bmgfonTranslate(res.Response[0].ResponseText);
          this.bmlfReportModal('open');
          this.maarraySelected = [];
          this.getDataList();
        }
      } else {
        this.toastMessageService.notifyError(res.Response[0].ResponseText);
      }
    });

  }

  bmlfHold(data) {
    if (data.Status == 'NEW') {
      
      let reqData: any = {
        JSON: JSON.stringify({
          HeaderData: [{
            JrnlBtchNo: data.JrnlBtchNo,
          }],
          ScreenName: [{
            CallType: this.msScreenName,
          }],
          Response: [{
            "ResponseText": "", "ErrorCode": ""
          }]
        })
      };
      this.ContraEntryService.bmlfonHoldData(reqData).subscribe(res => {
        if (res == null) {
          this.toastMessageService.notifyError('Server Error');
          return false;
        }
        if (res.Response[0].ErrorCode == "") {
          {
            // this.msReportDtl = this.objUtil.bmgfonTranslate(res.Response[0].ResponseText);
            // this.bmlfReportModal('open');
            this.toastMessageService.notifySuccess(res.Response[0].ResponseText);
            this.maarraySelected = [];
            this.getDataList();
          }
        } else {
          this.toastMessageService.notifyError(res.Response[0].ResponseText);
          this.maarraySelected = [];
        }
      });
    }
  }

  bmlfReportModal(psKey) {
    
    if (psKey == 'open') {
      this.mbReportDialog = true;
    } else if (psKey == 'close' || psKey == 'ok') {
      this.mbReportDialog = false;
    }
  }

  bmlFilterSetting() {
    this.filterEnabled = !this.filterEnabled;
  }

  //print 
  // Single Print
  bmlfonPrint(dataItem: any) {
    
  //  console.log("dataItem", dataItem)
    let psPosted: string = 'POSTED';
    if (dataItem.Status == 'NEW' || dataItem.Status == 'HOLD' || dataItem.Status == 'VOID') {
      psPosted = 'UNPOSTED';
    }
    try {
      let printReqParam: any = {
        "ReportName": "Contra Entry Report",
        "Status":psPosted,
        "NoFrom": dataItem.JrnlBtchNo,
        "NoTo": dataItem.JrnlBtchNo,
        "FromDate":'11/Jan/1900',
        "ToDate":'11/Jan/3001',
        "ListingFlag": true,
        "ListingVariable": dataItem.JrnlBtchNo
      }
      this.utilService.bmgfReportAction(printReqParam, 'Contra Entry').then((value) => {
        this.maarraySelected=[];
        this.getDataList();
      });

    } catch (e) {
      console.log(e);
    }
  }

  // Multiple Print
  onActionPrint() {
    
    let pbPosted: boolean = true;
    let pbUnposted: boolean = false;
    if (this.mbUnPosted == true) {
      pbPosted = false;
      pbUnposted = true;
    }
    let psPrintAll: string = '';
    let piCnt: number = 0;
    this.maarraySelected.forEach((ele) => {
      if (piCnt > 0) {
        psPrintAll =psPrintAll + '' + ele.JrnlBtchNo;
     } else {
         psPrintAll = ele.JrnlBtchNo;
     }
      ++piCnt;
    });
    try {
      let printReqParam: any = {
        "ReportName": "Contra Entry Report",
        "Status":this.msUnposted.toUpperCase(),
        "NoFrom": '',
        "NoTo": '',
        "FromDate":'11/Jan/1900',
        "ToDate":'11/Jan/3001',
        "ListingFlag": true,
        "ListingVariable": psPrintAll
      }
      this.utilService.bmgfReportAction(printReqParam, 'Contra Entry').then((value) => {
        this.maarraySelected=[];
        this.getDataList();
      });

    } catch (e) {
      console.log(e);
    }
  }
}

