  //---------------------------------------------------------------------------------------
  bmlfGetDefault() {
    let pobjReqData = {
      ScreenName: 'BankingGLEntryComponent',
      LookUpKey: 'DimensionKey',
    }
    this.userService.GETGeneralSqlData(pobjReqData).subscribe(res => {
      if (res.LookupData.length > 0) {
        res.LookupData.forEach(ele => {
          if (ele.DimensionKey == 'Dimension 1') {
            this.mArrGLEntryGrid1Col[6].Display = true; //Show Column
            this.mArrGLEntryGrid1Col[6].Title = ele.DimensionDesc;
          } else if (ele.DimensionKey == 'Dimension 2') {
            this.mArrGLEntryGrid1Col[7].Display = true; //Show Column
            this.mArrGLEntryGrid1Col[7].Title = ele.DimensionDesc;
          } else if (ele.DimensionKey == 'Dimension 3') {
            this.mArrGLEntryGrid1Col[8].Display = true; //Show Column
            this.mArrGLEntryGrid1Col[8].Title = ele.DimensionDesc;
          } else if (ele.DimensionKey == 'Dimension 4') {
            this.mArrGLEntryGrid1Col[9].Display = true; //Show Column
            this.mArrGLEntryGrid1Col[9].Title = ele.DimensionDesc;
          } else if (ele.DimensionKey == 'Dimension 5') {
            this.mArrGLEntryGrid1Col[10].Display = true; //Show Column
            this.mArrGLEntryGrid1Col[10].Title = ele.DimensionDesc;
          }

        });
      }
    });
    //+++++++++++++ Home Currency  ++++++++++++++++++++++++++
    let pobjReqData1 = {
      ScreenName: 'BankingGLEntryComponent',
      LookUpKey: 'CompanyInfo',
    }
    this.userService.GETGeneralSqlData(pobjReqData1).subscribe(res => {
      if (res.LookupData.length > 0) {
        this.msHomeCurrency = res.LookupData[0].HomeCurrency;
        this.mObjModel.CurrencyKey = res.LookupData[0].HomeCurrency;
        this.msHomeCurrencyDesc = res.LookupData[0].CurrencyDescription;
        this.msCurrencyDesc = res.LookupData[0].CurrencyDescription;
      }
    });

  }
  //---------------------------------------------------------------------------------------
  GetDefaultSeries() {
    //+++++++++++++ Default Series  ++++++++++++++++++++++++++
     let pobjReqData = {
      ScreenName: 'BankingGLEntryComponent',
      LookUpKey: 'BankingGLEntrySeries',
      Filter1: this.msscreenName,
      Filter2: this.msRecUserID
    }
    this.userService.GETGeneralSqlData(pobjReqData).subscribe(res => {
      if (res.LookupData.length > 0) {
        this.mObjModel.JrnlBtchNo = res.LookupData[0].DefaultSeries;
      }
    });
  }
  //---------------------------------------------------------------------------------------
  //Add
  bmlfonAdd() {

    //check Write permission
    if (!this.objUtils.mbWrite) {
      this.objToastService.notifyWarning(this.objUtils.bmgfonTranslate('COMMON.PERMISSION_WRITE'));
      return;
    };
    this.msCallFor = "Add";
    let pbResult = this.bmlfonValidateFormData();

    if (pbResult == true) {
      if (!this.mbChange && !this.FieldExtender.mbChange && !this.customCtrl.mbChange) {
        this.mObjModel.Posted = false;
        this.msStatus = 'NEW';
        this.mbPostToolButton = true;
        this.mbVoidToolButton = true;
        this.mbReleaseToolButton = true;
        this.mbHoldToolButton = true;
        this.bmlfonClearControl();
        this.bmlfHandleControls('NEW');
        this.bmlfGetDefault();
        this.GetDefaultSeries();
      }
      else {
        // Ask for Yes No Cancel
        this.mbShowDialog = true;
        //this.msCallFor = "Add";
      }
    }
  }

  //---------------------------------------------------------------------------------------
