  //---------------------------------------------------------------------------------------
  bmlfHandleControls(msCall) {

    //2 - Posted and 3- VOID
    if (this.msStatus == 'NEW' && this.mObjModel.JrnlBtchNo != '') {//if(this.mObjModel.Posted == true) {//Posted
      this.mbDiabledAllFields = false;
      this.mbDisabledPrimaryKey = true;
      this.mbCurrencyKey = false;
      this.mbDisableAcct = false;

      this.mbPostToolButton = false;
      this.mbHoldToolButton = false;
      this.mbVoidToolButton = true;
      this.mbReleaseToolButton = true;

    } if (this.msStatus == 'NEW' && this.mObjModel.JrnlBtchNo == '') {//if(this.mObjModel.Posted == true) {//Posted
      this.mbDiabledAllFields = false;
      this.mbDisabledPrimaryKey = false;
      this.mbCurrencyKey = false;
      this.mbDisableAcct = false;

      this.mbPostToolButton = false;
      this.mbHoldToolButton = true;
      this.mbVoidToolButton = true;
      this.mbReleaseToolButton = true;

    } else if (this.msStatus == 'HOLD') {//if(this.mObjModel.Posted == false){//Void
      this.mbDiabledAllFields = true;
      this.mbDisabledPrimaryKey = true;

      this.mbPostToolButton = true; //disable
      this.mbHoldToolButton = true;
      this.mbVoidToolButton = true;
      this.mbReleaseToolButton = false; //Active

      this.mbCurrencyKey = true;
      this.mbDisableAcct = true;

    } else if (this.msStatus == 'POSTED') {
      this.mbDiabledAllFields = true;
      this.mbDisabledPrimaryKey = true;
      this.mbPostToolButton = true;
      this.mbHoldToolButton = true;
      this.mbReleaseToolButton = true;
      this.mbVoidToolButton = false;

      this.mbCurrencyKey = true;
      this.mbDisableAcct = true;

    } else if (this.msStatus == 'VOID') {
      this.mbDiabledAllFields = true;
      this.mbDisabledPrimaryKey = true;
      this.mbPostToolButton = true;
      this.mbHoldToolButton = true;
      this.mbReleaseToolButton = true;
      this.mbVoidToolButton = true;

      this.mbCurrencyKey = true;
      this.mbDisableAcct = true;

    }

  }

  public bmlfGetHomeConversionAmt(parrGridData) {
    //+++++++++++++ Conversion Factor  ++++++++++++++++++++++++++

    let pnumCurrFact: number = 0;
    if (this.mObjModel.CurrencyKey == "" || this.msHomeCurrency == "" || this.mObjModel.CurrencyKey == this.msHomeCurrency) {
      this.mArrGLEntryGrid = parrGridData;
      this.bmlfCalculateTotal();
      return;
    }
    let psCustomDateFormat = "yyyy-MM-dd";
    let reqData: any = {
      JSON: JSON.stringify({
        HeaderData: [{
          FromCurrency: this.mObjModel.CurrencyKey,
          ToCurrency: this.msHomeCurrency,
          DATE: moment(this.mObjModel.AplDate).format(psCustomDateFormat.toUpperCase()),
          RateType: "B",
          ConversionFactor: "0.00",
        }],
        Response: [{ "ResponseText": "", "ErrorCode": "" }]
      })
    };
    this.objService.bmlfonGetFinanceCurrencyConversion(reqData).subscribe(res => {
      if (res == null) {
        this.mArrGLEntryGrid = parrGridData;
        this.bmlfCalculateTotal();
        this.objToastService.notifyError('Server Error');
        return;
      } else if (res.Response[0].ErrorCode == "") {
        if (res.HeaderData.length > 0) {
          pnumCurrFact = Number(res.HeaderData[0].ConversionFactor);
          parrGridData.forEach(ele => {
            if (pnumCurrFact > 0) {
              ele.DrAmt = Number(ele.DrAmt) * pnumCurrFact;
              ele.CrAmt = Number(ele.CrAmt) * pnumCurrFact;
            }
            ele.Flag = 'M';
          })
        }
      }
      this.mArrGLEntryGrid = parrGridData;
      this.bmlfCalculateTotal();
    });

  }

