  bmlfonAddGridLine() {


    //--------------Grid Validation-----------------
    let errRow: number = 0;
    let errFiled: string = '';
    if (this.mArrGLEntryGrid.length > 0) {
      this.mArrGLEntryGrid.forEach(ele => {
        if (errFiled == '') {
          if (ele.Acct == '') {
            //errRow += '' + errRow;
            errFiled = 'Account';
          }
          errRow += 1;
        }
      });

      if (errFiled == "Account") {
        this.objToastService.notifyError((this.objUtils.bmgfonTranslate('BANKINGGLENTRY.MSG.ROWENTRYBLANK') + ': ' + errRow));
        return false;
      }
    }
    //--------------Grid Validation-----------------
    //--------------End VAlidation------------
    const pobjDataCopy: any = Object.assign({}, this.mObjModelGrid1);

    //----------Row No - S
    let piLastIndex;
    if (this.mArrGLEntryGrid.length > 0) {
      piLastIndex = this.mArrGLEntryGrid.reduce((max, obj) => obj.DocLineNo > max ? obj.DocLineNo : max, this.mArrGLEntryGrid[0].DocLineNo);
    } else {
      piLastIndex = this.mArrGLEntryGrid.length;
    }

    pobjDataCopy.DocLineNo = Number(piLastIndex) + 1;


    //----------Row No - E
    pobjDataCopy.Flag = 'A';
    //pobjDataCopy.TDSType = this.mObjModel.TDSType
    //pobjDataCopy.TDSRate = this.mnTempTDSRate;

    //   if(pobjDataCopy.TDSRate != 0){
    //     pobjDataCopy.TDSAmt = pobjDataCopy.Extension * (pobjDataCopy.TDSRate/100);
    //   }else{
    //     pobjDataCopy.TDSAmt = 0;
    //   }

    this.mArrGLEntryGrid.push(pobjDataCopy);
  }
