  //---------------------------------------------------------------------------------------
  bmlfCalculateTotal() {
    this.mnTotalCreditAmount = 0;
    this.mnTotalDebitAmount = 0;
    this.mnDifferenceAmount = 0;

    this.mArrGLEntryGrid.forEach(ele => {
      this.mnTotalCreditAmount += ele.CrAmt;
      this.mnTotalDebitAmount += ele.DrAmt;
    });
    if (this.mnTotalCreditAmount > this.mnTotalDebitAmount) {
      this.mnDifferenceAmount = Number(this.mnTotalCreditAmount - this.mnTotalDebitAmount);
      this.msLblAmount = 'Dr.';
    }
    else {
      this.mnDifferenceAmount = Number(this.mnTotalDebitAmount - this.mnTotalCreditAmount);
      this.msLblAmount = 'Cr.';
    }


    //-----
  }
