  //---------------------------------------------------------------------------------------
  bmlfBankingGLPost() {

    if (!this.objUtils.mbWrite) {
      this.objToastService.notifyWarning(this.objUtils.bmgfonTranslate('COMMON.PERMISSION_WRITE'));
      return;
    };
    if (this.mbPostToolButton == true) {
      return;
    }

    if (this.mObjModel.JrnlBtchNo.trim() == "") {
      this.objToastService.notifyError((this.objUtils.bmgfonTranslate('BANKINGGLENTRY.MSG.SELECTSERIES')));
      setTimeout(function () { document.getElementById('txtJrnlBtchNo').focus(); }, 10);
      return;
    }

    if (this.mbInsert && this.mbChange == false && this.FieldExtender.mbChange == false && this.customCtrl.mbChange == false) {
      return;
    }

    this.msCallFor = "POST";
    if (this.mbChange || this.FieldExtender.mbChange || this.customCtrl.mbChange) {
      this.mbShowDialog = true;
      return;
    };

    //Validate posting date
    let tmpDate = new Date();
    if (this.mObjModel.PstDate > tmpDate) {
      this.objToastService.notifyError((this.objUtils.bmgfonTranslate('BANKINGGLENTRY.MSG.POSTINGDATECHK')));
      return false;
    }

    let reqData: any = {
      JSON: JSON.stringify({
        HeaderData: [{
          JrnlBtchNo: this.mObjModel.JrnlBtchNo
        }],
        Response: [{
          "ResponseText": "", "ErrorCode": ""
        }]
      })
    };
    this.mbIsLoading = true;
    this.objService.bmlfonPostBankingGLEntry(reqData).subscribe(res => {
      this.mbIsLoading = false;
      if (res.Response[0].ErrorCode == "") {
        //this.objToastService.notifySuccess(this.objUtils.bmgfonTranslate('COMMON.MSG.SAVE'));
        //this.objToastService.notifySuccess(res.Response[0].ResponseText);
        //else if (res.Response[0].ResponseText.toUpperCase().search('DELETED') != -1) {
        this.msMsgData.screenName = this.msscreenName;
        this.msMsgData.Data = res.Response[0].ResponseText;
        this.mbBulkResponseView = true;
        this.msReport = res.Response[0].ResponseText;
        this.mbReportDialog = true;
        if (res.Response[0].ResponseText.toUpperCase().search('FAILURE') != -1 || res.Response[0].ResponseText.toUpperCase().search('PERIOD NOT') != -1) {
          this.msStatus = 'NEW';
        } else {
          this.msStatus = 'POSTED';
        }

        this.bmlfGetDataOnLoad(this.mObjModel.JrnlBtchNo);
      }
    });
  }
