  // Get Lookup Description
  bmlfGetLookupDes(pobjData) {
    let parrSetKeyData = [
      { KeyCode: 'CURRENCY', KeyValue1: pobjData.CurrencyKey, KeyValue2: 'CurrencyKey', KeyDescription: '' },
      { KeyCode: 'ACCOUNT', KeyValue1: pobjData.Acct, KeyValue2: 'Acct', KeyDescription: '' },

    ]
    let reqData: any = {
      JSON: JSON.stringify({
        HeaderData: parrSetKeyData,
        ScreenName: [{ ScreenName: this.msscreenName }],
        Response: [{ ResponseText: '', ErrorCode: '' }]
      })
    };

    this.userService.bmlfFetchKeyDescription(reqData).subscribe(res => {
      if (res == null) {
        return false;
      }
      if (res.LookupData.length) {
        res.LookupData.forEach((ele) => {
          if (ele.KeyValue2 == 'CurrencyKey') {
            this.msCurrencyDesc = ele.KeyDescription;
          } else if (ele.KeyValue2 == 'Acct') {
            this.msAccDesc = ele.KeyDescription;
          }
        });
      }
    }, err => {
      console.log(err);
    });
  }
  //---------------------------------------------------------------------------------
  bmlfonDialogAction(action) {

    if (action == 'No') {
      this.mbShowDialog = false;
      this.mbChange = false;
      if (this.msCallFor == "FormClose") {
        this.router.navigate(['/main/bankcash/banking-gl-entry']);
      }
      else if (this.msCallFor == "Add") {
        this.bmlfonClearControl();
      }
      else if (this.msCallFor == "Copy") {
        this.bmlfonSetFormMode("I", "Copy");
      }
      else if (this.msCallFor == "Search") {
        this.bmlfOpenLookupModal('BankingGLEntrySearch', 'Banking GL Entry', 'BankingGLEntryList', 'click', '', 'custom', 'txtJrnlBtchNo');
        return;
      } else if (this.msCallFor == "POST") {
        this.bmlfBankingGLPost();
      }
      else {
        return;
      }
    }// if no
    else if (action == 'Yes' && this.msCallFor == "FormClose") {
      this.bmlfonSubmitData();
    }
    else if (action == 'Yes' && this.msCallFor == "Add") {
      this.bmlfonSubmitData();
    }
    else if (action == 'Yes' && this.msCallFor == "Copy") {
      this.bmlfonSubmitData();
    }
    else if (action == 'Yes' && this.msCallFor == "Search") {
      this.msCallFor = "SaveSearch";
      this.mbShowDialog = false;
      this.bmlfonSubmitData();
      //this.bmlfOpenLookupModal('BankingGLEntrySearch', 'Banking GL Entry', 'BankingGLEntryList', 'click', '', 'custom', 'txtJrnlBtchNo');
      //return;
    } else if (action == 'Yes' && this.msCallFor == "POST") {
      this.mbChange = true;
      this.bmlfonSubmitData();
    }
    else if (action == 'Yes') {
      // call update function
      this.mbChange = false;
      this.mbShowDialog = false;
      this.msFormMode = "U";
      this.mbDisabledPrimaryKey = true;
    }
    else {
      this.mbShowDialog = false;
    }
  }
