import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AuthGuard } from 'src/app/core/guards/auth.guard';

import { ContraEntryListComponent } from './components/transaction/contra-entry/contra-entry-list/contra-entry-list.component';
import { ContraEntryManageComponent } from './components/transaction/contra-entry/contra-entry-manage/contra-entry-manage.component';
import { VoidChkTranListComponent } from './components/transaction/void-chk-transaction/void-chk-tran-list/void-chk-tran-list.component';
import { VoidChkTranManageComponent } from './components/transaction/void-chk-transaction/void-chk-tran-manage/void-chk-tran-manage.component';


import { BankMasterListComponent } from './components/master/bank-master/bank-master-list/bank-master-list.component';
import { BankMasterManageComponent } from './components/master/bank-master/bank-master-manage/bank-master-manage.component';

import { CashMasterListComponent } from './components/master/cash-master/cash-master-list/cash-master-list.component';
import { CashMasterManageComponent } from './components/master/cash-master/cash-master-manage/cash-master-manage.component';

import { BankLayoutMasterListComponent } from './components/master/bank-layout-master/bank-layout-master-list/bank-layout-master-list.component';
import { BankLayoutMasterManageComponent } from './components/master/bank-layout-master/bank-layout-master-manage/bank-layout-master-manage.component';

import { PaymentModeMasterListComponent } from './components/master/payment-mode-master/payment-mode-master-list/payment-mode-master-list.component';
import { PaymentModeMasterManageComponent } from './components/master/payment-mode-master/payment-mode-master-manage/payment-mode-master-manage.component';

import { BankAccountTypeMasterListComponent } from './components/master/bank-account-type-master/bank-account-type-master-list/bank-account-type-master-list.component';
import { BankAccountTypeMasterManageComponent } from './components/master/bank-account-type-master/bank-account-type-master-manage/bank-account-type-master-manage.component';

import { TransactionReconcilationListComponent } from './components/transaction/transaction-reconcilation/transaction-reconcilation-list/transaction-reconcilation-list.component';
import { TransactionReconcilationManageComponent } from './components/transaction/transaction-reconcilation/transaction-reconcilation-manage/transaction-reconcilation-manage.component';
import { TransactionReconcilationManageeditComponent } from './components/transaction/transaction-reconcilation/transaction-reconcilation-manageedit/transaction-reconcilation-manageedit.component';

import { BankCashComponent } from './bank-cash.component';
import { BankingGLEntryListComponent } from './components/transaction/banking-gl-entry/banking-gl-entry-list/banking-gl-entry-list.component';
import { BankingGLEntryManageComponent } from './components/transaction/banking-gl-entry/banking-gl-entry-manage/banking-gl-entry-manage.component';
import { ContraComponent } from './components/report/contra/contra.component';
import { ContraDashboardComponent } from './components/report/contra/contra-dashboard.component';
import { BankDepositSlipComponent } from './components/report/bank-deposit-slip/bank-deposit-slip.component';
import {ReconciliationReportComponent} from './components/report/reconciliation-report/reconciliation-report.component';
import {BookCashBankSummaryReportComponent} from './components/report/book-cash-bank-summary-report/book-cash-bank-summary-report.component'

const routes: Routes = [
  {
    path: '',
    component: BankCashComponent,
    children:
      [
        {
          path: '', redirectTo: 'bankcash', pathMatch: 'full'
        },
        {
          path: 'contra-entry', component: ContraEntryListComponent, canActivate: [AuthGuard]
        },
        {
          path: 'contra-entry/manage', component: ContraEntryManageComponent, canActivate: [AuthGuard]
        },
        {
          path: 'void-chk-transaction', component: VoidChkTranListComponent, canActivate: [AuthGuard]
        },
        {
          path: 'void-chk-transaction/manage', component: VoidChkTranManageComponent, canActivate: [AuthGuard]
        },
        {
          path: 'bank-master', component: BankMasterListComponent, canActivate: [AuthGuard]
        },
        {
          path: 'bank-master/manage', component: BankMasterManageComponent, canActivate: [AuthGuard]
        },
        {
          path: 'cash-master', component: CashMasterListComponent, canActivate: [AuthGuard]
        },
        {
          path: 'cash-master/manage', component: CashMasterManageComponent, canActivate: [AuthGuard]
        },
        {
          path: 'bank-layout-master', component: BankLayoutMasterListComponent, canActivate: [AuthGuard]
        },
        {
          path: 'bank-layout-master/manage', component: BankLayoutMasterManageComponent, canActivate: [AuthGuard]
        },
        {
          path: 'payment-mode-master', component: PaymentModeMasterListComponent, canActivate: [AuthGuard]
        },
        {
          path: 'payment-mode-master/manage', component: PaymentModeMasterManageComponent, canActivate: [AuthGuard]
        },
        {
          path: 'bank-account-type-master', component: BankAccountTypeMasterListComponent, canActivate: [AuthGuard]
        },
        {
          path: 'bank-account-type-master/manage', component: BankAccountTypeMasterManageComponent, canActivate: [AuthGuard]
        },
        {
          path: 'transaction-reconciliation', component: TransactionReconcilationListComponent, canActivate: [AuthGuard]
        },
        {
          path: 'transaction-reconciliation/manage', component: TransactionReconcilationManageComponent, canActivate: [AuthGuard]
        },
        // {
        //   path: 'transaction-reconcilation/manageedit', component: TransactionReconcilationManageeditComponent, canActivate: [AuthGuard]
        // },
        {
          path: 'banking-gl-entry' , component: BankingGLEntryListComponent, canActivate: [AuthGuard]
        },
        {
          path: 'banking-gl-entry/manage' , component: BankingGLEntryManageComponent, canActivate: [AuthGuard]
        },
        {
          path: 'contra' , component: ContraComponent, canActivate: [AuthGuard]
        },
        {
          path: 'contra-dashboard' , component: ContraDashboardComponent, canActivate: [AuthGuard]
        },{
          path: 'bank-deposit-slip' , component: BankDepositSlipComponent, canActivate: [AuthGuard]
        },
        {
          path:'reconciliation-report', component: ReconciliationReportComponent, canActivate:[AuthGuard]
        },
        {
          path:'book-cash-bank-summary-report', component:BookCashBankSummaryReportComponent, canActivate:[AuthGuard]
        }
        
      ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class BankCashRoutingModule { }
