  // Delete Line Item with Header Grid1
  bmlfonDeleteLineItem(piRowId: any = null) {
    this.miGrid1DeleteLineIndex = piRowId;
    if (this.mArrGrid.length == 1) {
      this.mbdeleteLineItem = false;
      this.mbdeleteDialogView = true;
    } else if (this.mArrGrid.length > 1) {
      this.mbdeleteDialogView = false;
      this.mbdeleteLineItem = true;
    } else {
      this.mbdeleteLineItem = true;
    }
  }
  //Action Delete line with header
  bmlfdialogDeleteAction(actionType: any) {

    if (actionType == 'Yes') {
      if (this.mArrGrid.length == 1) {
        this.bmlonCompleteDel();
      } else {
        this.bmlfonDeleteGrid1Line();
      }
      this.mbdeleteDialogView = false;
    } else {
      this.mbdeleteDialogView = false;
    }
  }
  //Action Delete line
  bmlfdialogDeleteLineAction(action: any) {

    if (action == 'Yes') {
      this.bmlfonDeleteGrid1Line();
      this.mbdeleteLineItem = false;
    }
    if (action == 'No') {
      this.mbdeleteLineItem = false;
    }
  }

  bmlfonDeleteGrid1Line() {

    let pSrequestObj: any = {
      HeaderData: [{
        TemplateID: this.mObjModel.TemplateID,
        RowNum: this.mArrGrid[this.miGrid1DeleteLineIndex]["RowNum"]
      }],
      Response: [{ ResponseText: '', 'ErrorCode': '' }]
    };
    let reqData: any = {
      JSON: JSON.stringify(pSrequestObj)
    };

    this.objService.bmlfonDeleteLineData(reqData).subscribe(res => {

      if (res == null) { return false; }
      if (!this.isEmpty(res.Response[0].ErrorCode)) {
        this.objToastService.notifyError(res.Response[0].ResponseText);
      } else {
        this.objToastService.notifySuccess(this.appUtility.bmgfonTranslate('COMMON.MSG.LINEDELETE'));
        this.mArrGrid.splice(this.miGrid1DeleteLineIndex, 1);
      }
    });
  }
  //------------------------------------------------------------------------------------------------
  bmlonCompleteDel() {

    if (!this.isEmpty(this.mObjModel.TemplateID)) {
      let reqData: any = {
        JSON: JSON.stringify({
          HeaderData: [{
            TemplateID: this.mObjModel.TemplateID,
          }],
          Response: [{
            "ResponseText": "", "ErrorCode": ""
          }]
        })
      };
      this.objService.bmlfonDeleteData(reqData).subscribe(res => {
        if (res == null) { return false; }
        if (!this.isEmpty(res.Response[0].ErrorCode)) {
          this.objToastService.notifyError(res.Response[0].ResponseText);
        }
        else {
          this.objToastService.notifySuccess((this.appUtility.bmgfonTranslate('COMMON.MSG.DELETE')));
          this.bmlfonClearControl();
        }
      });
    }
  }

