  bmlfNgChanges(piRowId, psField) {

    this.miSelectedRowIndex = piRowId;
    this.mbChange = true;
    if (this.mArrGrid[piRowId].Flag.trim() != "A") {
      this.mArrGrid[piRowId].Flag = "M";
    }
    /* Abhay/03-July-2020/Bug# 140532 */
    if (psField == "ColumnHeading") {
      for (let index = 0; index < this.mArrGrid.length; index++) {
        if (this.mArrGrid[index]['ColumnHeading'] == this.mArrGrid[piRowId]['ColumnHeading'] && piRowId != index) {
          this.objToastService.notifyError((this.appUtility.bmgfonTranslate('BankLayoutMaster.Msg.Key7')));
          this.mArrGrid[piRowId]['ColumnHeading'] = "";
          document.getElementById("txtColumnHeading" + piRowId.toString()).focus;
          break;
        }
      }
    }
  }

  // Add Line Item Grid 1
  bmlfonAddLineToGrid() {

    if (!this.appUtility.mbWrite) {
      this.objToastService.notifyWarning(this.appUtility.bmgfonTranslate('COMMON.PERMISSION_WRITE'));
      return false;
    };
    if (this.mObjModel.TemplateID == undefined || this.mObjModel.TemplateID == '') {
      this.objToastService.notifyError((this.appUtility.bmgfonTranslate('BankLayoutMaster.Msg.Key')));
      return false;
    };

    const pobjDataCopy: any = Object.assign({}, this.mObjDetailModelBlank);
    let piLastIndex;
    piLastIndex = this.mArrGrid.length;

    if (piLastIndex > 0) {
      let index = this.mArrGrid.length - 1;
      if (this.isEmpty(this.mArrGrid[index]['Value'])) {
        this.objToastService.notifyError((this.appUtility.bmgfonTranslate('BankLayoutMaster.Msg.Key6')) + " " + index.toString());
        //let eleID = 'txtValue' + (index).toString();
        //setTimeout(function () { document.getElementById(eleID).focus(); }, 10);
      } else {
        pobjDataCopy.Flag = "A";
        pobjDataCopy.TemplateId = this.mObjModel.TemplateID;
        pobjDataCopy.RowNum = this.mArrGrid.length + 1;;
        pobjDataCopy.Recuserid = this.msUserId;
        pobjDataCopy.CreateUserID = this.msUserId;
        pobjDataCopy.ModifyUserID = this.msUserId;
        this.mArrGrid.push(pobjDataCopy);
        //let eleID = 'txtvalue' + (piLastIndex).toString();
        //setTimeout(() => { document.getElementById(eleID).focus(); }, 10);
      }
    } else {
      pobjDataCopy.Flag = "A";
      pobjDataCopy.TemplateId = this.mObjModel.TemplateID;
      pobjDataCopy.RowNum = piLastIndex + 1;
      pobjDataCopy.Recuserid = this.msUserId;
      pobjDataCopy.CreateUserID = this.msUserId;
      pobjDataCopy.ModifyUserID = this.msUserId;
      this.mArrGrid.push(pobjDataCopy);
      //let eleID = 'txtvalue' + (piLastIndex).toString();
      //setTimeout(() => { document.getElementById(eleID).focus(); }, 10);
    }
  }


